import { ChangeDetectionStrategy, Component, EventEmitter, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { Observable, Subscription } from 'rxjs';

import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { ConstraintService } from 'src/app/common/services/epop-api/constraint.service';
import { PlanningConstraintCategoriesQuery } from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';
import { PlanningConstraintsQuery } from 'src/app/common/store/planning-constraints/planning-constraint.query';

import { ConstraintListComponent } from '../constraint-list/constraint-list.component';

@Component({
    selector: 'epop-constraint-library',
    templateUrl: './constraint-library.component.html',
    styleUrls: ['./constraint-library.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintLibraryComponent implements OnInit, OnDestroy {

    @Output()
    public readonly closeList = new EventEmitter<void>();

    @Output()
    public readonly importConstraints = new EventEmitter<Array<PlanningConstraint>>();

    @ViewChild(ConstraintListComponent)
    public constraintListComponent: ConstraintListComponent;

    public constraintsList$: Observable<PlanningConstraint[]>;

    public visibleColumns: ConstraintListComponent['visibleColumns'] = ['checkboxSelector'];

    public selectedConstraints: Array<PlanningConstraint> = [];

    private subscriptions: Array<Subscription> = [];

    public constructor(
        public planningConstraintsQuery: PlanningConstraintsQuery,
        public planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery,
        public constraintService: ConstraintService,
        private translocoService: TranslocoService
    ) { }


    public ngOnInit(): void {
        // Get list of constraints :
        this.constraintsList$ = this.planningConstraintsQuery.selectImportableConstraints$();
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public isRowDisabled(constraint: PlanningConstraint): boolean {
        // Check if constraint is importable
        if (constraint) {
            return !this.planningConstraintsQuery.isConstraintImportable(constraint);
        } else {
            return false;
        }
    }

    public importSelectedConstraints(): void {
        this.importConstraints.emit(this.selectedConstraints);
    }

    public clearSelectedConstraints(): void {
        this.constraintListComponent.clearSelection();
    }

    public onSelectedConstraintsChanged(newConstraintList: Array<PlanningConstraint>): void {
        this.selectedConstraints = newConstraintList;
    }

    public getCheckboxTooltip(constraint: PlanningConstraint): string {
        if (this.isRowDisabled(constraint)) {
            return this.translocoService.translate('planningGenerator.planning.planningConstraint.import.errors.duplication');
        } else {
            return '';
        }
    }

}
