import { ChangeDetectionStrategy, Component } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { PlanningConstraintCategoriesQuery } from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';
import { PlanningConstraintsQuery } from 'src/app/common/store/planning-constraints/planning-constraint.query';

import { RequirementCellParams } from '../../../../../requirement-grid/components/requirement-cell/requirement-cell.component';

export type ConstraintCellParams = {
    data: FavoritePlanningConstraint | PlanningConstraint; // Resource name
    requirementDate: Date;
} & Omit<ICellRendererParams, 'data'>;

@Component({
    selector: 'epop-constraint-cell',
    templateUrl: './constraint-cell.component.html',
    styleUrls: ['./constraint-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintCellComponent implements ICellRendererAngularComp {

    public params: ConstraintCellParams;

    public defaultConstraintCellHeight = 46;

    public constructor(private planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery,
        private planningConstraintsQuery: PlanningConstraintsQuery,
        private translocoService: TranslocoService) {}


    public agInit(params: RequirementCellParams): void {
        this.params = params;
    }

    public refresh(_params: ICellRendererParams): boolean {
        // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    public onToggleCell(): void {
        this.params.node.expanded = !this.params.node.expanded;
        const newHeight = this.getRowHeight();
        this.params.node.setRowHeight(newHeight);
        this.params.api.onRowHeightChanged();
    }

    public getRowHeight(): number {
        return this.params.node.expanded ? null : this.defaultConstraintCellHeight;
    }

    public getRowHeightStyle(): string {
        const currentRowHeight = this.getRowHeight();
        if (currentRowHeight === null) {
            return 'auto';
        } else {
            return `${this.getRowHeight()}px`;
        }
    }

    public isPriorityOverriden(): boolean {
        const currentConstraint = this.params.data;
        if (currentConstraint && currentConstraint instanceof FavoritePlanningConstraint) {
            const libConstraint = this.getConstraintDef();
            const constraintCategory = this.planningConstraintCategoriesQuery.getEntity(libConstraint.getCategoryId());
            return currentConstraint.getPriority() !== constraintCategory.defaultPriority;
        } else {
            return false;
        }
    }

    public getOverridenPriorityMessage(): string {
        if (this.params.data instanceof FavoritePlanningConstraint) {
            return this.translocoService.translate('planningGenerator.planning.planningConstraint.priority.overriden', {
                newPriority: this.translocoService.translate<string>(`planningGenerator.planning.planningConstraint.priority.values.${this.params.data.getPriority()}`)
            });
        }
        return undefined;
    }

    public getConstraintDef(): PlanningConstraint {
        const currentConstraint = this.params.data;
        if (currentConstraint) {
            if (currentConstraint instanceof PlanningConstraint) {
                return currentConstraint;
            } else {
                return this.planningConstraintsQuery.getLibraryConstraint(currentConstraint);
            }
        }
        return undefined;
    }

}
