import { ChangeDetectionStrategy, Component, Input, OnChanges } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { TranslocoService } from '@ngneat/transloco';
import { Observable, of } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { ResourceDayAffectation } from 'src/app/common/models/planning/resource-day-affectation.model';
import { EventShiftsQuery } from 'src/app/common/store/event-shifts/event-shifts.query';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { ResourceAffectationsQuery } from 'src/app/common/store/resource-affectations/resource-affectations.query';
import { ShiftsQuery } from 'src/app/common/store/shifts/shifts.query';

import { DayAffectationDialogComponent, DayAffectationDialogData } from '../../../day-affectation-dialog/day-affectation-dialog.component';
import { ResourceAffectation } from './../../../../../../../../common/models/planning/resource-affectation.model';
import { Shift } from './../../../../../../../../common/models/planning/shift.model';

@Component({
    selector: 'epop-resource-affectation-cell',
    templateUrl: './resource-affectation-cell.component.html',
    styleUrls: ['./resource-affectation-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResourceAffectationCellComponent implements OnChanges {
    @Input()
    public affectationDay: Date;

    @Input()
    public resourceDayAffectation: ResourceDayAffectation;

    public allocationList: Array<{shift$: Observable<AbstractShift>; resourceAffectation$: Observable<ResourceAffectation>}>;
    public onDutyAffectation$: Observable<ResourceAffectation>;
    public onDutyShift$: Observable<Shift>;
    public resourceAffectation$: Observable<ResourceAffectation>;
    public shift$: Observable<AbstractShift>;
    public showPointMode$: Observable<boolean>;

    public constructor(private shiftsQuery: ShiftsQuery,
        private eventShiftsQuery: EventShiftsQuery,
        private dialog: MatDialog,
        private translocoService: TranslocoService,
        private resourceAffectationsQuery: ResourceAffectationsQuery,
        private planningEditionContextQuery: PlanningEditionContextQuery) {}

    public getBackgroundColor(shift: AbstractShift): string {
        if (shift instanceof Shift) {
            return shift?.getRgbBackgroundColor();
        } else {
            return 'transparent';
        }
    }

    public getBorderColor(shift: AbstractShift): string {
        if (shift instanceof EventShift) {
            return shift?.getRgbTextColor();
        } else {
            return 'transparent';
        }
    }

    public getTextColor(shift: AbstractShift): string {
        return shift?.getRgbTextColor();
    }

    public getTooltipMessage(resourceAffectation: ResourceAffectation, shift: AbstractShift): string {
        let message = '';
        if (shift instanceof Shift) {
            message += shift?.formatHours();
        } else {
            message += shift?.description;
        }
        const statusToString = this.statusToString(resourceAffectation);
        if (statusToString !== '') {
            message += ` - ${statusToString}`;
        }
        return message;
    }

    public isOnDutyShift(shift: AbstractShift): boolean {
        return (shift instanceof Shift) && shift.shiftType === 'onCallDuty';
    }

    public ngOnChanges(): void {
    // Update cell affectations :
        this.computeCellAffectations();
        // Show point mode :
        this.showPointMode$ = this.planningEditionContextQuery.selectCurrentCellContentType$().pipe(
            map(contentType => (contentType === 'onlyEvents' && this.resourceDayAffectation?.getDayType() !== 'event'))
        );
    }

    public openDetails(shift$: Observable<AbstractShift>, resourceAffectation$: Observable<ResourceAffectation>): void {
        this.dialog.open(DayAffectationDialogComponent, {
            autoFocus: false,
            panelClass: 'epop-dialog-pane--big-corner',
            data: {
                shift$: shift$,
                affectationDay: this.affectationDay,
                resourceAffectation$: resourceAffectation$
            } as DayAffectationDialogData
        });
    }

    private computeCellAffectations(): void {
    // Reset all cell affectations :
        this.allocationList = [];
        // Work affectaction & events
        this.resourceAffectation$ = this.resourceAffectationsQuery.selectEntity(this.resourceDayAffectation?.getResourceAffectationId());
        this.shift$ = this.resourceAffectation$.pipe(
            switchMap(affectation => this.getShiftFromContentType$(affectation))
        );
        if (this.resourceDayAffectation?.getResourceAffectationId()) {
            this.allocationList.push({
                resourceAffectation$: this.resourceAffectation$,
                shift$: this.shift$
            });
        }
        // On Duty affectations :
        if (this.resourceDayAffectation.onDutyAffectation !== undefined) {
            this.onDutyAffectation$ = this.resourceAffectationsQuery.selectEntity(this.resourceDayAffectation?.getOnDutyAffectationId());
            this.onDutyShift$ = this.onDutyAffectation$.pipe(
                switchMap(affectation => this.shiftsQuery.selectEntity(affectation?.getShiftId()))
            );
            this.allocationList.push({
                resourceAffectation$: this.onDutyAffectation$,
                shift$: this.onDutyShift$
            });
        }
    }

    private getShiftFromContentType$(resourceAffectation: ResourceAffectation): Observable<AbstractShift> {
        switch (this.resourceDayAffectation?.getDayType()) {
            case 'event':
                return this.eventShiftsQuery.selectEntity(resourceAffectation?.getShiftId());
            case 'workAffectation':
                return this.shiftsQuery.selectEntity(resourceAffectation?.getShiftId());
            default:
                return of(undefined as AbstractShift);
        }
    }

    private statusToString(resourceAffectation: ResourceAffectation): string {
        const statusLocaleKey: Array<string> = [];
        if (resourceAffectation?.isFrozen) {
            statusLocaleKey.push('shift.status.frozen');
        }
        if (resourceAffectation?.isPriority) {
            statusLocaleKey.push('shift.status.priority');
        }
        if (resourceAffectation?.isDesired) {
            statusLocaleKey.push('shift.status.desired');
        }

        return statusLocaleKey.map(statusKey => this.translocoService.translate<string>(statusKey)).join(' - ');
    }
}
