import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnDestroy, ViewChild } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef, ColGroupDef, GridApi, GridOptions, GridReadyEvent, RowNode } from 'ag-grid-community';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { filter, switchMap, tap } from 'rxjs/operators';

import { AgGridHelper } from 'src/app/common/core/grid/helpers/ag-grid.helper';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { PlanningEditionContext } from 'src/app/common/models/planning/planning-edition-context.model';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';

import {
    ColumnHeaderCollapserComponent, ColumnHeaderCollapserParams
} from '../../../../common/core/grid/components/column-header-collapser/column-header-collapser.component';
import { planningDimensions } from '../../planning-generator.page';
import { RequirementGridComponent } from '../requirement-grid/requirement-grid.component';
import {
    PlanningEditionContextHelper,
    PlanningSortingAttribute,
    PlanningSortingOrder
} from './../../../../common/models/planning/planning-edition-context.model';
import { ResourcePlanning } from './../../../../common/models/planning/resource-planning.model';
import { TeamPlanning } from './../../../../common/models/planning/team-planning.model';
import { RequirementGridComponentExpansion } from './../requirement-grid/requirement-grid.component';
import { PlanningCellComponent, PlanningCellParams } from './components/planning-cell/planning-cell.component';
import { PlanningColumnHeaderComponent, PlanningHeaderParams } from './components/planning-column-header/planning-column-header.component';
import { PlanningCountersCellComponent } from './components/planning-counters/planning-counters-cell/planning-counters-cell.component';
import {
    PlanningCountersHeaderComponent,
    PlanningCountersHeaderParams
} from './components/planning-counters/planning-counters-header/planning-counters-header.component';
import { ResourceCellComponent } from './components/resource-cell/resource-cell.component';
import { ResourceHeaderComponent, ResourceHeaderParams } from './components/resource-header/resource-header.component';

const planningGridGroupId = 'planningColumnsGroup';

@Component({
    selector: 'epop-planning-grid',
    templateUrl: './planning-grid.component.html',
    styleUrls: ['./planning-grid.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlanningGridComponent implements AfterViewInit, OnDestroy {
    @Input()
    public unit: UnitDetails;

    @ViewChild('fullScreenContainer')
    public fullScreenContainer: ElementRef;

    // Requirement grid :
    @ViewChild(RequirementGridComponent)
    public requirementGridComponent: RequirementGridComponent;

    public activePlanning$: Observable<TeamPlanning> = this.teamPlanningsQuery.selectActive() as Observable<TeamPlanning>;
    public columnDefs: Array<ColGroupDef> = [];
    public defaultIsCounterStateCollapsed = true;
    public frameworkComponents = {
        // eslint-disable-next-line @typescript-eslint/naming-convention
        ResourceHeaderComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        ResourceCellComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        PlanningCellComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        PlanningColumnHeaderComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        ColumnHeaderCollapserComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        PlanningCountersCellComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        PlanningCountersHeaderComponent
    };

    public gridApi: GridApi;
    public gridOptions: GridOptions = {
        alignedGrids: [],
        groupHeaderHeight: 0,
        onGridReady: (event: GridReadyEvent) => this.onGridReady(event),
        onGridSizeChanged: () => this.onGridResized$.next(),
        suppressChangeDetection: true,
        headerHeight: planningDimensions.planningHeaderHeight,
        getRowNodeId: (data: {id: string}) => data.id,
        animateRows: true,
        suppressContextMenu: true,
        defaultColDef: {
            suppressMenu: true,
            resizable: false,
            lockPosition: true
        }
    };

    public isGridPlanningCompacted: boolean;
    public planningAlignementSpace: number;
    public planningGridData: ResourcePlanning[] = [];
    public requirementGridOptions: GridOptions;

    // Planning grid :
    private activePlanningSubscription: Subscription;
    private currentSortAttribute: PlanningSortingAttribute;
    private currentSortOrder?: PlanningSortingOrder;
    private currentVisualizationPeriod: PlanningEditionContext['visualizationPeriod'];
    private currentZoomMode: PlanningEditionContext['zoomMode'];
    private onGridResized$ = new BehaviorSubject<void>(undefined);

    public constructor(private cdRef: ChangeDetectorRef,
        private teamPlanningsQuery: TeamPlanningsQuery,
        private planningEditionContextQuery: PlanningEditionContextQuery,
        private translocoService: TranslocoService) {}

    public getCurrentPlanningType$(): Observable<PlanningEditionContext['planningType']> {
        return this.planningEditionContextQuery.selectCurrentPlanningType$();
    }

    /**
   * Get spacing between window and the planning grid to align stuff on top of it.
   *
   * @returns
   */
    public getLeftPlanningOffset(): number {
        return planningDimensions.resourceColumnWidth;
    }

    public getPlanningColWidth(): number {
        const value = planningDimensions.planningColumnWidth[this.currentVisualizationPeriod] as number;
        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
        const width = (this.gridOptions.api as any).headerRootComp.eHeaderViewport.clientWidth as number;
        const visibleColumnCount = PlanningEditionContextHelper.getDaysCountFromVisuPeriod(this.currentVisualizationPeriod);
        let colWidth: number;
        if (visibleColumnCount !== undefined) {
            colWidth = width / visibleColumnCount;
        } else if (this.currentVisualizationPeriod === 'compacted') {
            colWidth = planningDimensions.planningColumnMinWidth;
        } else if (Number.isInteger(value)) {
            colWidth = value;
        } else {
            colWidth = planningDimensions.planningColumnWidth.normal;
        }
        return Math.max(planningDimensions.planningColumnMinWidth, colWidth);
    }

    public getRowHeight(): number {
        if (this.currentZoomMode) {
            return planningDimensions.planningRowHeight[this.currentZoomMode];
        } else {
            return planningDimensions.planningRowHeight.normal;
        }
    }

    public ngAfterViewInit(): void {
    // Get options from the other grid (requirements one)
        this.requirementGridOptions = this.requirementGridComponent.getGridOptions();
        this.subscribeToData();
        // Update template (mostly for the requirementGridOptions new value)
        this.cdRef.detectChanges();
    }

    public ngOnDestroy(): void {
        this.activePlanningSubscription.unsubscribe();
        this.activePlanningSubscription = undefined;
    }

    public onRequirementPlanningExpansion(expansion: RequirementGridComponentExpansion): void {
        this.isGridPlanningCompacted = expansion === 'full';
    }

    public updatePlanningAlignementSpace(isCollapsed: boolean): void {
        if (this.gridApi) {
            let width = (isCollapsed ? 0 : planningDimensions.countersColumnWidth) + planningDimensions.collapserColumnWidth;
            // Add spacing for vertical scroll bar width :
            width += AgGridHelper.getVerticalScrollWitdh(this.gridApi);
            this.planningAlignementSpace = width;
        } else {
            this.planningAlignementSpace = 0;
        }
    }

    private generateColumnDef(planning: TeamPlanning): void {
        this.columnDefs = [];
        // Resource grid :
        this.columnDefs[0] = this.getResourceGridDefinition();
        // Planning grid
        this.columnDefs[1] = this.getPlanningGridDefinition(planning);
        // Constraint grid :
        this.columnDefs[2] = this.getCountersGridDefinition();
    }

    private getCountersGridDefinition(): ColGroupDef {
        const counterColName = 'counters';
        return {
            marryChildren: true,
            children: [
                {
                    headerClass: 'epop-planning-grid-header__no-padding',
                    pinned: 'right',
                    width: planningDimensions.collapserColumnWidth,
                    colId: 'ColumnHeaderCollapserColumn',
                    headerComponent: 'ColumnHeaderCollapserComponent',
                    headerComponentParams: {
                        columnToCollapse: [counterColName],
                        defaultIsCollapsed: this.defaultIsCounterStateCollapsed,
                        onCollapse: isCollapsed => this.updatePlanningAlignementSpace(isCollapsed),
                        collapserTitle: this.translocoService.translate('planningGenerator.planning.affectations.model.counters.label'),
                        onGridResized$: this.onGridResized$
                    } as ColumnHeaderCollapserParams
                },
                {
                    headerClass: 'epop-planning-grid-header__no-padding',
                    field: counterColName,
                    pinned: 'right',
                    width: planningDimensions.countersColumnWidth,
                    cellClass: 'epop-planning-grid-cell',
                    cellRenderer: 'PlanningCountersCellComponent',
                    headerComponent: 'PlanningCountersHeaderComponent',
                    headerComponentParams: {
                        counterName: this.translocoService.translate('planningGenerator.planning.affectations.model.counters.hours')
                    } as PlanningCountersHeaderParams
                }
            ]
        };
    }

    private getPlanningGridDefinition(planning: TeamPlanning): ColGroupDef {
        return {
            marryChildren: true,
            groupId: planningGridGroupId,
            // Generate a column for each day :
            children: planning.getPlanningDays().map(date => ({
                width: planningDimensions.planningColumnWidth.normal,
                minWidth: planningDimensions.planningColumnMinWidth,
                enableCellChangeFlash: true,
                cellRenderer: 'PlanningCellComponent',
                cellClass: 'epop-planning-grid-cell',
                headerClass: DateHelperService.isWeekend(date) ? 'ag-header-cell--dayOff' : '',
                field: `planning.${DateHelperService.toHashString(date)}`,
                headerComponent: 'PlanningColumnHeaderComponent',
                cellRendererParams: {
                    unitId: this.unit?.getId(),
                    cellDate: date
                } as PlanningCellParams,
                headerComponentParams: {
                    planningDay: date
                } as PlanningHeaderParams
            } as ColDef))
        };
    }

    private getResourceGridDefinition(): ColGroupDef {
        return {
            marryChildren: true,
            children: [
                {
                    headerComponent: 'ResourceHeaderComponent',
                    field: 'resource',
                    pinned: 'left',
                    comparator: (valueA: number, valueB: number, _nodeA: RowNode, _nodeB: RowNode, isInverted: boolean): number => this.sortData(valueA, valueB, _nodeA, _nodeB, isInverted),
                    sort: 'asc',
                    width: planningDimensions.resourceColumnWidth,
                    cellRenderer: 'ResourceCellComponent',
                    cellClass: 'epop-planning-grid-cell',
                    headerComponentParams: {
                        resourceGroupName:
              this.translocoService.translate('planningGenerator.teamList.title', { unitName: this.unit?.getUnit().getName() }),
                        fullScreenElement: this.fullScreenContainer.nativeElement as HTMLElement
                    } as ResourceHeaderParams
                }
            ]
        };
    }

    private onGridReady(params: GridReadyEvent): void {
        this.gridApi = params.api;
        this.gridApi.setRowData(this.planningGridData);
        this.updatePlanningAlignementSpace(this.defaultIsCounterStateCollapsed);
        this.updateVisualizationPeriod(this.planningEditionContextQuery.getCurrentVisualizationPeriod());
    }

    /**
   * Refresh data of grid by detecting added, updated and removed rows.
   */
    private refreshGridData(newResourcePlannings: ResourcePlanning[]): void {
        AgGridHelper.refreshByTransactions(this.gridApi, this.planningGridData, newResourcePlannings);
        this.planningGridData = newResourcePlannings;
    }

    private sortData(valueA: number, valueB: number, _nodeA: RowNode, _nodeB: RowNode, isInverted: boolean): number {
        return this.planningEditionContextQuery.compareResources(valueA, valueB) * (isInverted ? -1 : 1);
    }

    private subscribeToData(): void {
    // Subscribe to current planning to show and generate headers accordingly:
        this.activePlanningSubscription = this.activePlanning$.pipe(
            // Wait for defined planning
            filter(planning => planning !== undefined),
            // Generate column from planning days
            tap(planning => this.generateColumnDef(planning)),
            // Get data planning data :
            switchMap(planning => this.teamPlanningsQuery.selectResourcePlannings$(planning.getId())),
            // Update member attributes :
            tap((resourcePlannings => this.refreshGridData(resourcePlannings)))
        ).subscribe();
        // Subscribe to zoomMode :
        this.planningEditionContextQuery.selectCurrentZoomMode$().subscribe(zoomMode => this.updateZoomMode(zoomMode));
        // Subscribe to visualisation period
        this.planningEditionContextQuery.selectCurrentVisualizationPeriod$()
            .subscribe(visualizationPeriod => this.updateVisualizationPeriod(visualizationPeriod));
        // Subscribe to sorting mode :
        this.planningEditionContextQuery.selectCurrentSortingData$()
            .subscribe(sortData => this.updateSortMode(sortData.sortingAttribute, sortData.sortingOrder));
    }

    private updateSortMode(newSortAttribute: PlanningSortingAttribute, newSortOrder?: PlanningSortingOrder): void {
        if (this.currentSortAttribute !== newSortAttribute) {
            this.gridApi?.refreshClientSideRowModel();
        }
        if (this.currentSortOrder !== newSortOrder) {
            this.gridOptions.columnApi?.applyColumnState({
                state: [{
                    colId: 'resource',
                    sort: newSortOrder
                }],
                defaultState: { sort: null }
            });
            this.currentSortAttribute = newSortAttribute;
        }
    }

    private updateVisualizationPeriod(visualizationPeriod: PlanningGridComponent['currentVisualizationPeriod']): void {
        this.currentVisualizationPeriod = visualizationPeriod;
        const children = this.gridOptions.columnApi?.getColumnGroup(planningGridGroupId)?.getChildren();
        if (children) {
            this.gridOptions.columnApi.setColumnWidths(
                children.map(col => (
                    {
                        key: col.getUniqueId(),
                        newWidth: this.getPlanningColWidth()
                    }
                ))
            );
        }
    }

    private updateZoomMode(zoomMode: PlanningEditionContext['zoomMode']): void {
        this.currentZoomMode = zoomMode;
        this.gridApi?.forEachNode(rowNode => {
            rowNode.setRowHeight(this.getRowHeight());
        });
        this.gridApi?.onRowHeightChanged();
    }
}
