import { add } from 'date-fns';
import { date, identifier, list, object, serializable } from 'serializr';

import { DateHelperService } from '../../helpers/DateHelper.service';
import { ObjectHelper } from '../../helpers/ObjectHelper';
import { ResourcePlanning } from './resource-planning.model';

export class TeamPlanning {

    @serializable(identifier())
    public id: number;

    @serializable
    public name: string;

    @serializable(date())
    public startDate: Date;

    @serializable(date())
    public endDate: Date;

    @serializable(list(object(ResourcePlanning)))
    public resourcePlannings: Array<ResourcePlanning['id']> = [];

    public constructor(init: Partial<TeamPlanning>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): TeamPlanning['id'] {
        return this.id;
    }

    public getResourcePlanningsIds(): Array<ResourcePlanning['id']> {
        return this.resourcePlannings;
    }

    public getName(): string {
        return this.name;
    }

    public getStartDate(): Date {
        return this.startDate;
    }

    public getEndDate(): Date {
        return this.endDate;
    }

    public getPeriod(): [Date, Date] {
        return [this.getStartDate(), this.getEndDate()];
    }

    /**
     * Get all single days of the planning
     *
     * @param startDate
     * @param endDate
     */
    public getPlanningDays(): Array<Date> {
        return DateHelperService.getAllDays(this.getStartDate(), add(this.getEndDate(), { days: 1 }));
    }

}
