import { ChangeDetectionStrategy, Component, ViewChild } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

import { AppPageContainerComponent } from 'src/app/common/components/app-page-container/app-page-container.component';
import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';

import { ConstraintLibraryComponent } from './components/constraint-library/constraint-library.component';


@Component({
    selector: 'epop-constraint-list-dialog',
    templateUrl: './constraint-list-dialog.component.html',
    styleUrls: ['./constraint-list-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstrainstListDialogComponent {

    @ViewChild(AppPageContainerComponent)
    public pageContainerComponent: AppPageContainerComponent;

    @ViewChild(ConstraintLibraryComponent)
    public constraintLibraryComponent: ConstraintLibraryComponent;

    public selectedConstraint: FavoritePlanningConstraint;

    public constraintsToImport: Array<PlanningConstraint>;


    public constructor(
        public dialogRef: MatDialogRef<ConstrainstListDialogComponent>
    ) { }

    public editConstraint(constaint: FavoritePlanningConstraint): void {
        this.selectedConstraint = constaint;
        this.goToEditionPage();
    }

    public onEditionCompleted(): void {
        this.goToFavListPage();
        this.selectedConstraint = undefined;
    }

    public onImportConstraints(constraintList: Array<PlanningConstraint>): void {
        this.constraintsToImport = constraintList;
        this.goToImportPage();
    }

    public onImportCompleted(): void {
        this.goToFavListPage();
        this.constraintLibraryComponent?.clearSelectedConstraints();
        this.constraintsToImport = undefined;
    }

    public goToFavListPage(): void {
        this.pageContainerComponent.gotToPage(0);
    }

    public goToEditionPage(): void {
        this.pageContainerComponent.gotToPage(1);
    }

    public goToConstraintLibPage(): void {
        this.constraintsToImport = undefined;
        this.pageContainerComponent.gotToPage(2);
    }

    public goToImportPage(): void {
        this.pageContainerComponent.gotToPage(3);
    }

}
