import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { PlanningConstraintCategory } from 'src/app/common/models/planning/planning-constraint-category.model';
import { FavoritePlanningConstraintsStore } from 'src/app/common/store/favorite-planning-constraints/favorite-planning-constraint.store';

import { PlanningConstraintTag } from '../../models/planning/planning-constraint-tag.model';
import { PlanningConstraintsStore } from '../../store/planning-constraints/planning-constraint.store';
import { PlanningConstraintCategoriesStore } from './../../store/planning-constraint-categories/planning-constraint-categories.store';
import { PlanningConstraintTagsStore } from './../../store/planning-constraint-tags/planning-constraint-tags.store';
import { EpopService } from './abstract-epop.service';

@Injectable({
    providedIn: 'root'
})
export class ConstraintService extends EpopService {

    public constructor(
        httpClient: HttpClient,
        private planningConstraintCategoriesStore: PlanningConstraintCategoriesStore,
        private planningConstraintTagsStore: PlanningConstraintTagsStore,
        private favPlanningConstraintsStore: FavoritePlanningConstraintsStore,
        private planningConstraintsStore: PlanningConstraintsStore
    ) {
        super(httpClient);
    }

    public getResourcePath(): string {
        return 'constraints';
    }

    /**
     * Fetch and store library of constraints
     */
    public refreshAllLibraryConstraints(): void {
        this.getEntity$<Array<PlanningConstraint>>(PlanningConstraint).pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((constraints: Array<PlanningConstraint>) => this.planningConstraintsStore.upsertConstraints(constraints));
    }


    /**
     * Get constraints category of planning
     */
    public refreshAllConstraintCategories(): void {
        this.getEntity$<Array<PlanningConstraintCategory>>(PlanningConstraintCategory, undefined, 'constraint-categories').pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((categories: Array<PlanningConstraintCategory>) => this.planningConstraintCategoriesStore.set(categories));
    }

    /**
     * Get constraints category of planning
     */
    public refreshAllConstraintTags(): void {
        this.getEntity$<Array<PlanningConstraintTag>>(PlanningConstraintTag, undefined, 'constraint-tags').pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((tags: Array<PlanningConstraintTag>) => this.planningConstraintTagsStore.set(tags));
    }

    public upsertFavoriteConstraint(constraint: FavoritePlanningConstraint, selectedWeight: number, selectedPriority: number, selectedPeriodType: FavoritePlanningConstraint['periodType'], selectedStartPeriod: Date, selectedEndPeriod: Date, targetDefinition: FavoritePlanningConstraint['targetDefinition']): void {
        this.favPlanningConstraintsStore.setConstraintAttributes(constraint, selectedWeight, selectedPriority, selectedPeriodType, selectedStartPeriod, selectedEndPeriod, targetDefinition);
    }

    /**
     * Update favorite constraints params
     */
    public updateFavoriteConstraintParamValue(favConstraintId: number, newValue: FavoritePlanningConstraint['params']): void {
        this.favPlanningConstraintsStore.setConstraintParamValue(Number(favConstraintId), newValue);
    }

    /**
     * Update favorite constraints state (enable/disable)
     */
    public updateFavoriteConstraintStateValue(favConstraintId: number, isEnable: boolean): void {
        this.favPlanningConstraintsStore.setConstraintStateValue(Number(favConstraintId), isEnable);
    }

}
