import { ChangeDetectionStrategy, Component, OnChanges, OnDestroy, SimpleChanges, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { Observable, Subscription } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';

import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';
import { ResourceDayAffectation } from 'src/app/common/models/planning/resource-day-affectation.model';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { ResourceAffectationsQuery } from 'src/app/common/store/resource-affectations/resource-affectations.query';
import { ResourceDayAffectationsQuery } from 'src/app/common/store/resource-day-affectations/resource-day-affectations.query';

import {
    NewAffectationDialogComponent,
    NewAffectationResultData,
    NewAffectionDialogData
} from '../new-affectation-dialog/new-affectation-dialog.component';
import { PlanningHeaderParams } from '../planning-column-header/planning-column-header.component';
import { ResourcePlanning } from './../../../../../../common/models/planning/resource-planning.model';
import { PlanningService } from './../../../../../../common/services/epop-api/planning.service';
import { PlanningCellMenuComponent } from './components/planning-cell-menu/planning-cell-menu.component';

export type PlanningCellParams = {
    value: ResourceDayAffectation['id']; // array of allocation names
    unitId: number;
    cellDate: Date;
} & Omit<ICellRendererParams, 'value'>;

@Component({
    selector: 'epop-planning-cell',
    templateUrl: './planning-cell.component.html',
    styleUrls: ['./planning-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlanningCellComponent implements ICellRendererAngularComp, OnDestroy {
    @ViewChild(PlanningCellMenuComponent)
    public planningCellMenu: PlanningCellMenuComponent;

    public params: PlanningCellParams;
    public resourceAffectation: ResourceAffectation;
    public resourceDayAffection: ResourceDayAffectation;

    private subscriptions: Array<Subscription> = [];

    public constructor(
        private resourceDayAffectationsQuery: ResourceDayAffectationsQuery,
        private resourceAffectationsQuery: ResourceAffectationsQuery,
        private planningService: PlanningService,
        private planningEditionContextQuery: PlanningEditionContextQuery,
        private dialog: MatDialog) {}

    public agInit(params: PlanningCellParams): void {
        this.params = params;
        this.subscriptions.push(
            this.resourceDayAffectationsQuery.selectEntity(this.params.value).subscribe(a => this.resourceDayAffection = a)
        );
        this.subscriptions.push(
            this.resourceDayAffectationsQuery.selectEntity(this.params.value).pipe(
                filter(v => !!v),
                switchMap(dayAffectation => this.resourceAffectationsQuery.selectEntity(dayAffectation?.getResourceAffectationId()))
            ).subscribe(a => this.resourceAffectation = a)
        );
    }

    public hasFrozenAffectation$(): Observable<boolean> {
        return this.planningEditionContextQuery.selectCurrentCellContentType$().pipe(
            map(contentType => {
                if (contentType !== 'onlyEvents' || this.resourceDayAffection?.getDayType() === 'event') {
                    return this.resourceAffectation?.getIsFrozen();
                }
                return false;
            })
        );
    }

    public isDayOff(): boolean {
        if (this.params?.cellDate && DateHelperService.isWeekend(this.params.cellDate)) {
            return true;
        }
        return false;
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public onDeleteResourcePlanning(): void {
    // ResourcePlanning
        const resourcePlanningId = this.getCurrentResourcePlanningId();
        const planningDate = this.getCurrentPlanningDay();
        this.planningService.removeResourceAffectation(resourcePlanningId, planningDate);
    }

    public onDoubleClick(): void {
        this.planningCellMenu.editResourceAffectation();
    }

    /**
   * Return false to prevent propagation of click event.
   */
    public onOpenMenu(event: MouseEvent): boolean {
        event.stopPropagation();
        this.planningCellMenu.openMenu();
        return false;
    }

    public refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    /**
   * Open a dialog to add a new affectation in a planning cell
   *
   * @param cellData
   */
    public showNewResourceAffectationDialog(cellResourceDayAffection: ResourceDayAffectation): void {
        const planningDate = this.getCurrentPlanningDay();
        // Open the dialog :
        const dialogRef = this.dialog.open(NewAffectationDialogComponent, {
            autoFocus: false,
            data: {
                date: planningDate,
                resourceAffection: cellResourceDayAffection ?
                    this.resourceAffectationsQuery.getEntity(cellResourceDayAffection.getResourceAffectationId()) : undefined,
                resourceDayAffection: cellResourceDayAffection,
                unitId: this.params.unitId
            } as NewAffectionDialogData
        });

        // When closed, update the store :
        this.subscriptions.push(
            dialogRef.afterClosed().subscribe((dialogResult: NewAffectationResultData) => {
                if (dialogResult !== undefined) {
                    const resourcePlanningId = this.getCurrentResourcePlanningId();
                    this.addNewResourceAffectation(cellResourceDayAffection, resourcePlanningId, planningDate, dialogResult);
                }
            })
        );
    }

    private addNewResourceAffectation(
        cellResourceDayAffection: ResourceDayAffectation,
        resourcePlanningId: number,
        planningDay: Date,
        dialogResult: NewAffectationResultData): void {
        const shift = dialogResult.selectedOccupationShift;
        const onDutyShift = dialogResult.selectedOnDuty;
        if (shift === undefined && onDutyShift === undefined) {
            // If the user select "No affectation" for all form, then delete this resource affectation :
            this.onDeleteResourcePlanning();
        } else {
            this.planningService.setResourceAffectations(
                resourcePlanningId, cellResourceDayAffection, planningDay, shift, dialogResult.currentOccupationShiftType, onDutyShift?.getId()
            );
        }
    }

    private getCurrentPlanningDay(): Date {
        const currentColumParams: PlanningHeaderParams = this.params.colDef.headerComponentParams as PlanningHeaderParams;
        return currentColumParams.planningDay;
    }

    private getCurrentResourcePlanningId(): ResourcePlanning['id'] {
        return (this.params.data as ResourcePlanning)?.getId();
    }
}
