import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output, QueryList, ViewChildren } from '@angular/core';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { PlanningConstraintCategoriesQuery } from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';

import { ConstraintFormComponent } from '../constraint-form/constraint-form.component';


@Component({
    selector: 'epop-constraint-import',
    templateUrl: './constraint-import.component.html',
    styleUrls: ['./constraint-import.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintImportComponent implements OnInit {

    @Input()
    public constraintsToImport: Array<PlanningConstraint>;

    @Output()
    public readonly importFinished = new EventEmitter<void>();

    @Output()
    public readonly importCanceled = new EventEmitter<void>();

    @ViewChildren(ConstraintFormComponent)
    public constraintFormComponents: QueryList<ConstraintFormComponent>;

    public newFavConstraints: Array<FavoritePlanningConstraint>;

    public constructor(private planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery) {}


    public ngOnInit(): void {
        this.newFavConstraints = this.constraintsToImport.map(constraint => {
            const constraintCategory = this.planningConstraintCategoriesQuery.getEntity(constraint.getCategoryId());
            return FavoritePlanningConstraint.generateFavoriteConstraint(constraint, constraintCategory);
        });
    }


    public onSaveExit(): void {
        this.constraintFormComponents.forEach(constraint => {
            constraint.saveConstraint();
        });
        this.importFinished.emit();
    }

    public onCancelEdition(): void {
        this.importCanceled.emit();
    }

    public editionHasError(): boolean {
        let hasError = false;
        this.constraintFormComponents?.forEach(component => {
            if (component.formHasErrors()) {
                hasError = true;
            }
        });
        return hasError;
    }

    public deleteNewFavorite(constraint: FavoritePlanningConstraint): void {
        const index = this.newFavConstraints.indexOf(constraint);
        if (index > -1) {
            this.newFavConstraints.splice(index, 1);
        }
    }

}
