import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';

export interface TextInputChangeEvent {
    inputKey: string; value: number;
}

export interface MultiInputTextParams {
    text: string;
    defaultValues: Record<string, number>;
}

interface RenderElement {
    type: 'input' | 'text';
    key: string;
}

@Component({
    selector: 'epop-multi-input-text',
    templateUrl: './multi-input-text.component.html',
    styleUrls: ['./multi-input-text.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiInputTextComponent implements OnInit, OnChanges {

    @Input()
    public multiInputparams: MultiInputTextParams;

    @Input()
    public readonly: string;

    @Output()
    public readonly textInputChange: EventEmitter<TextInputChangeEvent> = new EventEmitter<TextInputChangeEvent>();

    public renderElements: Array<RenderElement>;

    public getRenderElements(): void {
        this.renderElements = [];
        const regex = /{\w*}/g;
        const stringPattern = this.multiInputparams.text.split(regex);
        const stringInputs = this.multiInputparams.text.match(regex);
        stringPattern.forEach((pattern, index) => {
            this.renderElements.push(
                {
                    type: 'text',
                    key: pattern
                });
            if (stringInputs && index < stringInputs.length) {
                this.renderElements.push({
                    type: 'input',
                    key: stringInputs[index].slice(1, -1) // slice bracets to only get variable name
                });
            }
        });
    }

    public ngOnInit(): void {
        this.getRenderElements();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.multiInputparams) {
            this.getRenderElements();
        }
    }

    public onInputChanged(renderElmt: RenderElement, event: Event): void {
        const element = event.target as HTMLInputElement;
        this.textInputChange.emit({
            inputKey: renderElmt.key,
            value: Number(element.value)
        });
    }
}
