import { ChangeDetectionStrategy, Component } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { ResourceHeaderParamsDialogComponent } from './components/resource-header-params-dialog/resource-header-params-dialog.component';

export interface ResourceHeaderParams {
    resourceGroupName: string;
    fullScreenElement: HTMLElement;
}

@Component({
    selector: 'epop-resource-header',
    templateUrl: './resource-header.component.html',
    styleUrls: ['./resource-header.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResourceHeaderComponent {

    public params: ResourceHeaderParams;

    public constructor(private dialog: MatDialog) {}

    agInit(params: ResourceHeaderParams): void {
        this.params = params;
    }

    openResourceParamsDialog(): void {
        this.dialog.open(ResourceHeaderParamsDialogComponent, {
            autoFocus: false
        });
    }

    requestFullScreen(): void {
        if (this.isFullscreenActive()) {
            this.exitFullScreen();
        } else {
            this.goFullScreen();
        }
    }

    public isFullscreenActive(): boolean {
        return !!document.fullscreenElement;
    }


    public goFullScreen(): void {
        void this.params.fullScreenElement.requestFullscreen();
    }

    public exitFullScreen(): void {
        void document.exitFullscreen();
    }

}
