import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { ShiftsStore } from 'src/app/common/store/shifts/shifts.store';

import { EpopService } from './abstract-epop.service';

@Injectable({
    providedIn: 'root'
})
export class ShiftService extends EpopService {
    public constructor(
        httpClient: HttpClient,
        private shiftsStore: ShiftsStore
    ) {
        super(httpClient);
    }

    /**
     * Get list of event shifts
     */
    public getAllEventShifts$(): Observable<Array<EventShift>> {
        return this.getEntity$<Array<EventShift>>(EventShift, undefined, 'eventShifts').pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        );
    }

    public getResourcePath(): string {
        return 'shifts';
    }

    public updateShift(shiftId: number, backgroundColor?: string, textColor?: string, code?: string): void {
        this.shiftsStore.updateShift(shiftId, backgroundColor, textColor, code);
    }
}
