import { identifier, map, object, serializable } from 'serializr';

import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';

import { ObjectHelper } from '../../helpers/ObjectHelper';
import { Person } from './../person.model';
import { ResourceDayAffectation } from './resource-day-affectation.model';

export class ResourcePlanning {

    @serializable(identifier())
    public id: number;

    @serializable(object(Person))
    public resource: Person['id'];

    @serializable(map(object(ResourceDayAffectation)))
    public planning: Record<string, ResourceDayAffectation['id']> = {};

    public constructor(init: Partial<ResourcePlanning>, deepCopy = false) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init, deepCopy);
        }
    }

    getId(): ResourcePlanning['id'] {
        return this.id;
    }


    public getResourceId(): Person['id'] {
        return this.resource;
    }

    public getResourceName(): string {
        return this.resource.toString();
    }

    public getPlanning(): Record<string, ResourceDayAffectation['id']> {
        return this.planning;
    }

    public getAllDayAffectationIds(): Array<ResourceDayAffectation['id']> {
        return Object.values(this.planning);
    }

    public getResourceDayAffectations(date: Date): ResourceDayAffectation['id'] {
        return this.planning[DateHelperService.toHashString(date)];
    }

    public setResourceDayAffectations(date: Date, resourceDayAffectation: ResourceDayAffectation['id']): void {
        this.planning[DateHelperService.toHashString(date)] = resourceDayAffectation;
    }

    public removeResourceDayAffectations(date: Date): void {
        this.planning[DateHelperService.toHashString(date)] = undefined;
    }

    public setPlanning(planning: ResourcePlanning['planning']): void {
        this.planning = planning;
    }

}
