import { identifier, object, serializable } from 'serializr';

import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';

import { ObjectHelper } from './../../helpers/ObjectHelper';
import { Shift } from './shift.model';

export class ResourceAffectation {

    @serializable(identifier())
    public id: number;

    @serializable(object(Shift))
    public shift: AbstractShift['id'];

    @serializable
    public isDesired: boolean;

    @serializable
    public isPriority: boolean;

    @serializable
    public isFrozen: boolean;

    public constructor(init: Partial<ResourceAffectation>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

    public getShiftId(): number {
        return this.shift;
    }

    public getIsDesired(): boolean {
        return this.isDesired;
    }

    public getIsPriority(): boolean {
        return this.isPriority;
    }

    public getIsFrozen(): boolean {
        return this.isFrozen;
    }

    public setFrozen(isFrozen: boolean): void {
        this.isFrozen = isFrozen;
    }

    public setIsDesired(isDesired: boolean): void {
        this.isDesired = isDesired;
    }

    public setIsPriority(isPriority: boolean): void {
        this.isPriority = isPriority;
    }

    public setShiftId(shiftId: number): void {
        this.shift = shiftId;
    }


}
