import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef, GridApi, GridOptions, GridReadyEvent, RowNode } from 'ag-grid-community';

import { AgGridHelper } from 'src/app/common/core/grid/helpers/ag-grid.helper';
import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';

import { DialogShiftListSizes } from '../time-shift-dialog/shift-list-dialog.component';
import {
    IconColumHeaderComponent,
    IconColumHeaderParams
} from './../../../../../../../../common/core/grid/components/icon-column-header/icon-column-header.component';
import { ShiftActionsCellComponent, ShiftActionsCellParams } from './components/shift-actions-cell/shift-actions-cell.component';
import { ShiftColorCellComponent, ShiftColorCellParams } from './components/shift-color-cell/shift-color-cell.component';

export interface ShiftColorChangedEvent {
    color: string;
    id: number;
}

@Component({
    selector: 'epop-shift-grid',
    templateUrl: './shift-grid.component.html',
    styleUrls: ['./shift-grid.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ShiftGridComponent implements OnChanges {

    @Output()
    public readonly shiftBackgroundColorChanged = new EventEmitter<ShiftColorChangedEvent>();

    @Output()
    public readonly shiftDeleted = new EventEmitter<number>();

    @Output()
    public readonly shiftTextColorChanged = new EventEmitter<ShiftColorChangedEvent>();

    @Output()
    public readonly shiftsSelected = new EventEmitter<Array<AbstractShift>>();

    @Input()
    public additionalColumnDefs: Array<ColDef> = [];

    @Input()
    public isGridReadOnly = false;

    @Input()
    public selectedShifts: Array<AbstractShift> = [];

    @Input()
    public selectionMode: boolean;

    @Input()
    public shiftList: Array<AbstractShift> = [];

    @Input()
    public showActionCell: boolean;

    @Input()
    public showShiftBackgroundColorColumn = true;

    @Input()
    public textSearch: string;

    public actionCell: ColDef = {
        field: 'id',
        headerName: '',
        cellRenderer: 'ShiftActionsCellComponent',
        width: DialogShiftListSizes.actionColumn,
        cellRendererParams: {
            onDelete: id => this.shiftDeleted.next(id)
        } as ShiftActionsCellParams
    };

    public columnDefs: Array<ColDef> = [];

    public frameworkComponents = {
        // eslint-disable-next-line @typescript-eslint/naming-convention
        ShiftActionsCellComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        ShiftColorCellComponent,
        // eslint-disable-next-line @typescript-eslint/naming-convention
        IconColumHeaderComponent
    };

    public gridApi: GridApi;
    public gridOptions: GridOptions = {
        onGridReady: params => this.onGridReady(params),
        isRowSelectable: (node: RowNode) => !(node.data as AbstractShift).isNotRemovable(),
        suppressContextMenu: true,
        defaultColDef: {
            suppressMenu: true
        }
    };

    public selectionCell: ColDef = {
        headerCheckboxSelection: true,
        checkboxSelection: true,
        headerCheckboxSelectionFilteredOnly: true,
        cellClass: 'epop-shift-list__cell__selection',
        headerClass: 'epop-shift-list__col__selection',
        width: 20
    };

    public constructor(
        private translocoService: TranslocoService
    ) {}

    public get backgroundColorCol(): ColDef {
        return {
            field: 'rgbBackgroundColor',
            headerComponent: 'IconColumHeaderComponent',
            headerComponentParams: {
                iconName: 'mdi/format-color-fill'
            } as IconColumHeaderParams,
            cellRenderer: 'ShiftColorCellComponent',
            width: DialogShiftListSizes.colorColumn,
            cellRendererParams: {
                onColorChanged: (id, color) => this.shiftBackgroundColorChanged.next({ id, color }),
                readOnly: this.isGridReadOnly
            } as ShiftColorCellParams
        };
    }

    // Column definition :
    public get defaultColumnDefs(): Array<ColDef> {
        return [
            {
                field: 'rgbTextColor',
                headerComponent: 'IconColumHeaderComponent',
                headerComponentParams: {
                    iconName: 'mdi/format-color-text'
                } as IconColumHeaderParams,
                cellRenderer: 'ShiftColorCellComponent',
                width: DialogShiftListSizes.colorColumn,
                cellRendererParams: {
                    onColorChanged: (id, color) => this.shiftTextColorChanged.next({ id, color }),
                    readOnly: this.isGridReadOnly
                } as ShiftColorCellParams
            },
            {
                headerName: this.translocoService.translate('shift.code'),
                field: 'code',
                flex: 1,
                sortable: true,
                cellClass: 'epop-shift-list__cell__code'
            }
        ];
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.additionalColumnDefs || changes.selectionMode || changes.showActionCell) {
            this.columnDefs = [];
            if (changes.selectionMode) {
                this.columnDefs.push(this.selectionCell);
            }
            if (this.showShiftBackgroundColorColumn) {
                this.columnDefs.push(this.backgroundColorCol);
            }
            this.columnDefs.push(...this.defaultColumnDefs.concat(this.additionalColumnDefs));
            if (this.showActionCell) {
                this.columnDefs.push(this.actionCell);
            }
        }
        if (changes.shiftList && this.gridApi) {
            if (changes.shiftList.isFirstChange()) {
                this.gridApi.setRowData(changes.shiftList.currentValue);
            } else {
                AgGridHelper.refreshByTransactions(this.gridApi, changes.shiftList.previousValue, changes.shiftList.currentValue);
            }
        }
        if (changes.selectedShifts) {
            this.updateSelectedRows();
        }
    }

    public onSelectionChanged(): void {
        const userSelectedShifts = this.gridApi.getSelectedRows();
        const allSelectedRows = this.shiftList.filter(s => s.isNotRemovable()).concat(userSelectedShifts);
        this.shiftsSelected.next(allSelectedRows);
    }

    private onGridReady(params: GridReadyEvent): void {
        this.gridApi = params.api;
        this.gridApi.setRowData(this.shiftList);
        this.updateSelectedRows();
    }

    private updateSelectedRows(): void {
        if (this.gridApi) {
            this.gridApi.forEachNode(node => {
                if (this.selectedShifts.find(s => s.getId() === (node.data as AbstractShift).id) !== undefined) {
                    node.setSelected(true);
                } else {
                    node.setSelected(false);
                }
            });
        }
    }
}
