import { serializable } from 'serializr';

import { ObjectHelper } from '../../helpers/ObjectHelper';

export class PlanningConstraintTag {

    @serializable
    public id?: number;

    @serializable
    public labelFr?: string;

    public constructor(init: Partial<PlanningConstraintTag>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

}
