import { ChangeDetectionStrategy, Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { TranslocoService } from '@ngneat/transloco';
import { Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { EventShift } from 'src/app/common/models/planning/event-shift.model';
import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';
import { Shift } from 'src/app/common/models/planning/shift.model';

export interface DayAffectationDialogData {
    affectationDay: Date;
    resourceAffectation$: Observable<ResourceAffectation>;
    shift$: Observable<AbstractShift>;
}

interface InfoItem {
    label: string;
    value: string;
}

type AffectationStatus = 'desired' | 'frozen' | 'priority';

@Component({
    selector: 'epop-day-affectation-dialog',
    templateUrl: './day-affectation-dialog.component.html',
    styleUrls: ['./day-affectation-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DayAffectationDialogComponent implements OnInit {
    public dialogTitle$: Observable<string>;
    public infoItems$: Observable<Array<InfoItem>>;
    public shiftColor$: Observable<string>;
    public statusItems$: Observable<Array<AffectationStatus>>;

    public constructor(
        public dialogRef: MatDialogRef<DayAffectationDialogComponent>,
        private translocoService: TranslocoService,
        private dateHelperService: DateHelperService,
        @Inject(MAT_DIALOG_DATA) public data: DayAffectationDialogData
    ) { }

    public getStatusIcon(status: AffectationStatus): string {
        switch (status) {
            case 'desired':
                return 'mdi/star';
            case 'frozen':
                return 'mdi/lock-outline';
            case 'priority':
                return 'mdi/arrow-up-thin-circle-outline';
            default:
                return undefined;
        }
    }

    public ngOnInit(): void {
        // Dialog title :
        this.dialogTitle$ = this.data.shift$.pipe(
            switchMap(shift => {
                const code = shift.getCode();
                const translateKey = this.getTitleKeyFromShift(shift);
                return this.translocoService.selectTranslate<string>(translateKey, { code });
            })
        );

        // Shift color :
        this.shiftColor$ = this.data.shift$.pipe(
            map((shift: AbstractShift) => shift.getMainColor())
        );

        this.infoItems$ = this.data.shift$.pipe(
            map(shift => this.computeAttributes(shift))
        );

        this.statusItems$ = this.data.resourceAffectation$.pipe(
            map(resourceAffectation => this.computeStatus(resourceAffectation))
        );
    }

    private computeAttributes(abstractShift: AbstractShift): Array<InfoItem> {
        const items: Array<InfoItem> = [
            // Affectation date :
            {
                label: this.translocoService.translate('shift.date'),
                value: this.dateHelperService.localeLongFormat(this.data.affectationDay)
            }
        ];
        // Custom attributes :
        if (abstractShift instanceof Shift) {
            items.push(...this.computeShiftCustomAttributes(abstractShift));
        } else if (abstractShift instanceof EventShift) {
            items.push(...this.computeEventCustomAttributes(abstractShift));
        }
        return items;
    }

    private computeEventCustomAttributes(shift: EventShift): Array<InfoItem> {
        return [
            // Affectation date :
            {
                label: this.translocoService.translate('shift.description'),
                value: shift.description
            }
        ];
    }

    private computeShiftCustomAttributes(shift: Shift): Array<InfoItem> {
        return [
            // Affectation date :
            {
                label: this.translocoService.translate('shift.hours'),
                value: this.translocoService.translate('shift.period', {
                    startTime: shift.startTime,
                    endTime: shift.endTime
                })
            },
            // Break duration :
            {
                label: this.translocoService.translate('shift.shiftType.label'),
                value: this.translocoService.translate(`shift.shiftType.${shift.shiftType}`)
            },
            // Break duration :
            {
                label: this.translocoService.translate('shift.breakDurationLongLabel'),
                value: DateHelperService.durationToString(0, shift.nbMinutesBreakDuration)
            }
        ];
    }

    private computeStatus(resourceAffectation: ResourceAffectation): Array<AffectationStatus> {
        const items: Array<AffectationStatus> = [];
        if (resourceAffectation.isDesired) {
            items.push('desired');
        }
        if (resourceAffectation.isFrozen) {
            items.push('frozen');
        }
        if (resourceAffectation.isPriority) {
            items.push('priority');
        }
        return items;
    }

    private getTitleKeyFromShift(shift: AbstractShift): string {
        if (shift instanceof EventShift) {
            return 'planningGenerator.planning.affectations.model.eventHoursWithCode';
        } else {
            return 'planningGenerator.planning.affectations.model.workHoursWithCode';
        }
    }
}
