import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import { StringHelper } from 'src/app/common/helpers/StringHelper';
import { Person } from 'src/app/common/models/person.model';
import { AgeOption } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { JobTitle } from 'src/app/common/models/planning/job-title.model';

import { AppLocalesService } from './../../../../../core/transloco/app-locales.service';
import { JobTitlesQuery } from './../../../../../store/job-titles/job-titles.query';

/**
 * 1: female
 * 2: male
 * 3: other
 * (according to person model)
 */
export interface GenderOption {
    genderId: Person['gender'];
    label: string;
}

export interface CollaboratorFilterState {
    selectedFunctions: Array<number>;
    selectedGender: Array<Person['gender']>;
    selectedAges: Array<AgeOption>;
    selectedWorkRate: Array<number>;
}

@Component({
    selector: 'epop-collaborator-filters',
    templateUrl: './collaborator-filters.component.html',
    styleUrls: ['./collaborator-filters.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class CollaboratorFiltersComponent implements OnInit {

    @Input()
    public defaultValues: CollaboratorFilterState;

    @Output()
    public readonly filterChange = new EventEmitter<CollaboratorFilterState>();

    // Job titles :
    public selectedFunctions: Array<JobTitle>;
    public functionList$: Observable<Array<JobTitle>>;

    // Gender :
    public selectedGender: Array<Person['gender']>;
    public genderList: Array<GenderOption> = ['1', '2', '3'].map(
        id => ({
            genderId: id,
            label: this.translocoService.translate(`person.gender.${id}`)
        })
    );

    // Age :
    public selectedAges: Array<AgeOption>;
    public ageList: Array<AgeOption> = [
        {
            minAge: 0,
            maxAge: 16
        },
        {
            minAge: 0,
            maxAge: 18
        },
        {
            minAge: 60,
            maxAge: undefined
        }
    ];

    // Work rate :
    public selectedWorkRate: Array<number>;
    public workRateList: Array<number> = Array.from({ length: 10 }, (_v, k) => (k + 1) * 10).reverse(); // Generate array of rates

    public constructor(private jobTitlesQuery: JobTitlesQuery, private translocoService: TranslocoService, private appLocalesService: AppLocalesService) { }

    ngOnInit(): void {
        this.functionList$ = this.jobTitlesQuery.selectAll().pipe(
            tap(functionList => {
                if (this.defaultValues?.selectedFunctions) {
                    this.selectedFunctions = this.defaultValues.selectedFunctions.map(fId => functionList.find(f => f.id === fId));
                }
            })
        );
        if (this.defaultValues) {
            this.selectedGender = this.defaultValues.selectedGender;
            this.selectedAges = this.defaultValues.selectedAges?.map(ageOption => this.ageList.find(a => a.minAge === ageOption.minAge && a.maxAge === ageOption.maxAge));
            this.selectedWorkRate = this.defaultValues.selectedWorkRate;
        }
    }

    public clearFunctions(event: Event): void {
        this.selectedFunctions = [];
        this.afterClear(event);
    }

    public clearAges(event: Event): void {
        this.selectedAges = [];
        this.afterClear(event);
    }

    public clearGender(event: Event): void {
        this.genderList = [];
        this.afterClear(event);
    }

    public clearWorkRate(event: Event): void {
        this.selectedWorkRate = [];
        this.afterClear(event);
    }

    public afterClear(event: Event): void {
        event.stopPropagation();
        this.onFilterChanged();
    }

    public onFilterChanged(): void {
        this.filterChange.emit({
            selectedFunctions: this.selectedFunctions?.map(f => f.id),
            selectedGender: this.selectedGender,
            selectedAges: this.selectedAges,
            selectedWorkRate: this.selectedWorkRate
        });
    }

    public getFunctionLabel(funct: JobTitle): string {
        return funct.getFullName(this.appLocalesService.getCurrentAppLanguageCode());
    }

    public getAgeLabel(ageOption: AgeOption): string {
        return StringHelper.ageIntervalToString(this.translocoService, ageOption.minAge, ageOption.maxAge);
    }

    public getSelectedFunctionsLabels(): string {
        return this.selectedFunctions?.map(f => f.getAbbreviation(this.appLocalesService.getCurrentAppLanguageCode())).join(', ');
    }

}
