import { ChangeDetectionStrategy, Component, OnDestroy } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { Observable, Subscription } from 'rxjs';
import { filter, switchMap } from 'rxjs/operators';

import { CellUpdateFlasherHelper } from 'src/app/common/core/grid/helpers/cell-update-flasher.helper';
import { RequirementValue } from 'src/app/common/models/planning/requirement-value.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';

import { PlanningRequirementsQuery } from './../../../../../../common/store/planning-requirements/planning-requirements.query';

export type RequirementCellParams = {
    value: RequirementValue; // Resource name
    requirementDate: Date;
} & Omit<ICellRendererParams, 'value'>;

type CellStatus = 'ok' | 'warning' | 'error';

@Component({
    selector: 'epop-requirement-cell',
    templateUrl: './requirement-cell.component.html',
    styleUrls: ['./requirement-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RequirementCellComponent implements ICellRendererAngularComp, OnDestroy {
    public params: RequirementCellParams;
    public requirementValue: number;

    private cellUpdateHelper: CellUpdateFlasherHelper;
    private subscriptions: Array<Subscription> = [];

    public constructor(private planningRequirementsQuery: PlanningRequirementsQuery, private teamPlanningsQuery: TeamPlanningsQuery) {}

    public agInit(params: RequirementCellParams): void {
        this.params = params;
        const requirementId: number = (this.params.data as RequirementValue).getId();
        this.cellUpdateHelper = new CellUpdateFlasherHelper(params);
        this.subscriptions.push(
            this.computeRequirementValue$(requirementId).subscribe(value => {
                if (this.requirementValue !== value) {
                    this.requirementValue = value;
                    this.cellUpdateHelper.onUpdate();
                }
            })
        );
    }

    public getRequirementStatusClass(): CellStatus {
        const minValue = this.params.value?.getMinValue();
        if (this.requirementValue === minValue) {
            return 'ok';
        } else if (this.requirementValue < minValue) {
            return 'error';
        } else {
            return 'warning';
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    private computeRequirementValue$(requirementId: number): Observable<number> {
        return (this.teamPlanningsQuery.selectActive() as Observable<TeamPlanning>).pipe(
            filter(p => !!p),
            switchMap(teamPlanning => this.planningRequirementsQuery.selectEntity(requirementId).pipe(
                switchMap(requirement => {
                    const date = new Date(this.params.requirementDate);
                    return this.planningRequirementsQuery.computeRequirementValue$(requirement, teamPlanning, date);
                })
            )
            )
        );
    }
}
