import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Person } from 'src/app/common/models/person.model';
import { PlanningEditionContext } from 'src/app/common/models/planning/planning-edition-context.model';

import { AppLocalesService } from './../../../core/transloco/app-locales.service';
import { JobTitleService } from './../../../services/epop-api/job-title.service';
import { JobTitlesQuery } from './../../../store/job-titles/job-titles.query';

@Component({
    selector: 'epop-person-cell',
    templateUrl: './person-cell.component.html',
    styleUrls: ['./person-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PersonCellComponent implements OnInit {
    @Input() public person: Person;
    @Input() public zoomMode: PlanningEditionContext['zoomMode'];
    public jobTitleName$: Observable<string>;

    public constructor(
        private jobTitlesQuery: JobTitlesQuery,
        private appLocalesService: AppLocalesService,
        private jobTitleService: JobTitleService) {}

    public ngOnInit(): void {
    // Fetch and store Jobtitle
        this.jobTitleService.refreshJobTitle(this.person.getJobTitleId());
        this.jobTitleName$ = this.jobTitlesQuery.selectEntity(this.person.getJobTitleId()).pipe(
            map(jobTitle => jobTitle?.getName(this.appLocalesService.getCurrentAppLanguageCode()))
        );
    }
}
