import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { FavoritePlanningConstraintsStore } from 'src/app/common/store/favorite-planning-constraints/favorite-planning-constraint.store';
import { ResourceAffectationsStore } from 'src/app/common/store/resource-affectations/resource-affectations.store';
import { TeamPlanningsStore } from 'src/app/common/store/team-plannings/team-plannings.store';

import { ObjectHelper } from '../../helpers/ObjectHelper';
import { Person } from '../../models/person.model';
import { AbstractShift } from '../../models/planning/abstract-shift.model';
import { PlanningRequirement } from '../../models/planning/planning-requirement.model';
import { ResourceAffectation } from '../../models/planning/resource-affectation.model';
import { DayShiftType, ResourceDayAffectation } from '../../models/planning/resource-day-affectation.model';
import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { Shift } from '../../models/planning/shift.model';
import { TimeCounter } from './../../models/planning/time-counter.model';
import { PlanningRequirementsStore } from './../../store/planning-requirements/planning-requirements.store';
import { ResourceAffectationsQuery } from './../../store/resource-affectations/resource-affectations.query';
import { EpopService } from './abstract-epop.service';
import { EpopNormalizedData, epopSchema } from './epop-normalized-model.definition';

@Injectable({
    providedIn: 'root'
})
export class PlanningService extends EpopService {
    public constructor(
        httpClient: HttpClient,
        private teamPlanningsStore: TeamPlanningsStore,
        private planningRequirementsStore: PlanningRequirementsStore,
        private resourceAffectationsQuery: ResourceAffectationsQuery,
        private resourceAffectationsStore: ResourceAffectationsStore,
        private favoritePlanningConstraintsStore: FavoritePlanningConstraintsStore
    ) {
        super(httpClient);
    }

    public deletePlanningRequirement(requirement: PlanningRequirement): void {
        this.planningRequirementsStore.deleteRequirementPlanning(requirement);
    }

    /**
     * Get resource planning by ID. The data will be normalized.
     */
    public getPlanning$(
        planningId: TeamPlanning['id']
    ): Observable<EpopNormalizedData> {
        return this.getNormalizedEntity$(
            TeamPlanning,
            epopSchema.teamPlanningSchema,
            planningId.toString()
        );
    }

    /**
     * Get constraints of planning
     */
    public getFavoritePlanningConstraints$(
        planningId: TeamPlanning['id']
    ): Observable<Array<FavoritePlanningConstraint>> {
        return this.getEntity$<Array<FavoritePlanningConstraint>>(
            FavoritePlanningConstraint,
            planningId.toString(),
            '',
            'constraints'
        );
    }

    /**
     * Delete constraints of planning
     */
    public deleteFavoritePlanningConstraints(favoriteConstraintId: FavoritePlanningConstraint['id']
    ): void {
        this.favoritePlanningConstraintsStore.remove(favoriteConstraintId);
    }

    /**
     * Get counters of planning
     */
    public getPlanningCounters$(
        planningId: TeamPlanning['id']
    ): Observable<Array<TimeCounter>> {
        return this.getEntity$<Array<TimeCounter>>(
            TimeCounter,
            planningId.toString(),
            '',
            'hours-counters'
        );
    }

    /**
     * Get requirements of planning
     */
    public getPlanningRequirement$(
        planningId: TeamPlanning['id']
    ): Observable<Array<PlanningRequirement>> {
        return this.getEntity$<Array<PlanningRequirement>>(
            PlanningRequirement,
            planningId.toString(),
            '',
            'planning-requirements'
        );
    }

    /**
     * Get requirements of planning
     */
    public getPlanningResources$(
        planningId: TeamPlanning['id']
    ): Observable<Array<Person>> {
        return this.getEntity$<Array<Person>>(
            Person,
            planningId.toString(),
            '',
            'planning-resources'
        );
    }

    public getResourcePath(): string {
        return 'teamPlannings';
    }

    public removeResourceAffectation(
        resourcePlanningId: ResourcePlanning['id'],
        date: Date
    ): void {
        this.teamPlanningsStore.removeResourceAffectation(
            resourcePlanningId,
            date
        );
    }

    public setResourceAffectations(
        resourcePlanningId: ResourcePlanning['id'],
        resourceDayAffectation: ResourceDayAffectation,
        planningDay: Date,
        shift: AbstractShift,
        currentShiftType: DayShiftType,
        onDutyShiftId: Shift['id']
    ): void {
        // Updated affectation :
        let occupationAffectation: ResourceAffectation;
        let dutyAffection: ResourceAffectation;
        if (shift !== undefined) {
            // Check occupationAffectation :
            const occupationAffectationId =
                resourceDayAffectation?.getResourceAffectationId();
            if (
                occupationAffectationId !== undefined &&
                resourceDayAffectation.getDayType() === currentShiftType
            ) {
                // Only update the shiftId :
                this.resourceAffectationsStore.updateResourceShift(
                    occupationAffectationId,
                    shift.getId()
                );
                occupationAffectation =
                    this.resourceAffectationsQuery.getEntity(
                        occupationAffectationId
                    );
            } else {
                // Create a new affectation
                occupationAffectation = this.createResourceAffectation(
                    shift.getId()
                );
            }
        }
        if (onDutyShiftId !== undefined) {
            // Check occupationAffectation :
            const ondutyAffectionId =
                resourceDayAffectation?.getOnDutyAffectationId();
            if (ondutyAffectionId !== undefined) {
                // Only update the shiftId :
                this.resourceAffectationsStore.updateResourceShift(
                    ondutyAffectionId,
                    onDutyShiftId
                );
                dutyAffection =
                    this.resourceAffectationsQuery.getEntity(ondutyAffectionId);
            } else {
                // Create a new affectation
                dutyAffection = this.createResourceAffectation(onDutyShiftId);
            }
        }
        this.teamPlanningsStore.setResourceAffectation(
            resourcePlanningId,
            planningDay,
            occupationAffectation,
            currentShiftType,
            dutyAffection
        );
    }

    public updatePlanningRequirementValues(
        requirement: PlanningRequirement,
        newPlanningValues: Record<string, number>,
        requiredShiftIds: number[],
        requiredJobTitle: number[]
    ): void {
        this.planningRequirementsStore.updateRequirementPlanningValues(
            requirement,
            newPlanningValues,
            requiredShiftIds,
            requiredJobTitle
        );
    }

    private createResourceAffectation(shiftId: number): ResourceAffectation {
        return new ResourceAffectation({
            id: ObjectHelper.generateUniqueId(),
            shift: shiftId
        });
    }
}
