import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { TranslocoService } from '@ngneat/transloco';
import { filter, finalize, tap } from 'rxjs/operators';

import { RestData, restDataErrorStrategy } from 'src/app/common/components/rest-data';
import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningEditionContext } from 'src/app/common/models/planning/planning-edition-context.model';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { ConstraintService } from 'src/app/common/services/epop-api/constraint.service';
import { EpopNormalizedData } from 'src/app/common/services/epop-api/epop-normalized-model.definition';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { FavoritePlanningConstraintsStore } from 'src/app/common/store/favorite-planning-constraints/favorite-planning-constraint.store';
import { EpopStoreHelper } from 'src/app/common/store/helpers/epop-store-helper.service';
import { PlanningCountersStore } from 'src/app/common/store/planning-counters/planning-counters.store';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { PlanningRequirementsStore } from 'src/app/common/store/planning-requirements/planning-requirements.store';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';
import { TeamPlanningsStore } from 'src/app/common/store/team-plannings/team-plannings.store';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';

import { PageHeaderAction }
    from '../main-layout/components/page-container/components/page-container-header/page-container-header.component';
import { LayoutPage } from '../main-layout/models/layout-page.model';
import { MainLayoutStore } from '../main-layout/store/main-layout.store';
import { TimeCounter } from './../../common/models/planning/time-counter.model';
import { PlanningService } from './../../common/services/epop-api/planning.service';
import { ConstrainstListDialogComponent } from './components/constraint-list-dialog/constraint-list-dialog.component';
import {
    PlanningGeneratorDialogComponent,
    PlanningGeneratorDialogData
} from './components/planning-generator-dialog/planning-generator-dialog.component';
import { PlanningGeneratorRouteResolver } from './planning-generator-routing.resolver';

// Definition of planning grid dimensions :
export const planningDimensions = {
    planningHeaderHeight: 66,
    planningRowHeight: {
        normal: 50,
        compacted: 40,
        ultraCompact: 25
    },
    planningColumnMinWidth: 43,
    planningColumnWidth: {
        normal: 90
    },
    resourceColumnWidth: 250,
    countersColumnWidth: 110,
    collapserColumnWidth: 50
};

@Component({
    selector: 'epop-planning-generator',
    styleUrls: ['./planning-generator.page.scss'],
    templateUrl: './planning-generator.page.html',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlanningGeneratorPage extends LayoutPage implements AfterViewInit, OnDestroy {
    public currentTeamPlanning = new RestData<EpopNormalizedData>();
    public pageHeaderActions: Array<PageHeaderAction> = [
        {
            icon: 'mdi/content-save',
            onClick: (): void => this.savePlanning(),
            position: 'left',
            isDisabled: (): boolean => false,
            type: 'big'
        },
        {
            icon: 'mdi/play-circle',
            onClick: (): void => this.openPlanningGeneratorDialog(),
            position: 'left',
            isDisabled: (): boolean => this.planningType === 'planned'
        },
        {
            icon: 'mdi/link-variant',
            onClick: (): void => this.openConstraintDialog(),
            position: 'left',
            isDisabled: (): boolean => false
        },
        {
            icon: 'mdi/download',
            onClick: (): void => this.downloadCurrentPlanning(),
            position: 'right',
            isDisabled: (): boolean => false
        }
    ];

    public planningType: PlanningEditionContext['planningType'];
    public unitDetails: UnitDetails;
    public unitId: number;

    private serverErrorMessage: string;

    public constructor(
        private resourcePlanningService: PlanningService,
        private unitService: UnitService,
        private teamPlanningsStore: TeamPlanningsStore,
        private planningRequirementsStore: PlanningRequirementsStore,
        private planningCountersStore: PlanningCountersStore,
        private favoritepPlanningConstraintsStore: FavoritePlanningConstraintsStore,
        private constraintService: ConstraintService,
        private epopStoreManager: EpopStoreHelper,
        private teamPlanningsQuery: TeamPlanningsQuery,
        private planningEditionContextQuery: PlanningEditionContextQuery,
        private translocoService: TranslocoService,
        private dialog: MatDialog,
        private unitDetailsQuery: UnitDetailsQuery,
        private cdRef: ChangeDetectorRef,
        private route: ActivatedRoute,
        mainLayoutStore: MainLayoutStore
    ) {
        super(mainLayoutStore);
        // Get unit ID from URL in contructor to prevent ExpressionChangedAfterItHasBeenCheckedError
        this.unitId = Number.parseInt(this.route.snapshot.paramMap.get('unitId'), 10);
    }

    public ngAfterViewInit(): void {
        this.fetchUnit(this.unitId);
        this.unitDetailsQuery.selectEntity(this.unitId).pipe(
            filter(u => u && (this.unitDetails === undefined || this.unitDetails.getGeneratedPlanningId() !== u.getGeneratedPlanningId()))
        ).subscribe(unitDetails => {
            this.unitDetails = unitDetails;
            // Update page title :
            this.setPageTitle();
            // fetch planning depending of the current type selected :
            this.planningEditionContextQuery.selectCurrentPlanningType$().subscribe(type => {
                this.planningType = type;
                // reset active planning :
                this.updateActivePlanning(null);
                let planningId: number;
                if (type === 'planned') {
                    planningId = unitDetails.getPlannedPlanningId();
                } else if (type === 'onPreparation') {
                    planningId = unitDetails.getOnPreparationPlanningId();
                } else if (type === 'generated') {
                    planningId = unitDetails.getGeneratedPlanningId();
                }
                this.fetchPlanning(planningId);
                // fetch requirements:
                this.fetchPlanningRequirements(planningId);
                // fetch counters:
                this.fetchPlanningCounters(planningId);
                // fetch constraints :
                this.fetchPlanningConstraints(planningId);
                this.fetchPlanningConstraintCategories();
            });
            // fetch shifts :
            this.fetchFavoriteEventShift(this.unitId);
            this.fetchFavoriteShift(this.unitId);
            // TODO : Planning Shifts (events&timecode) must be fetched separatly, as they can difffer with favourites ones :
            // Load error message :
            this.translocoService.selectTranslate('messages.error', {}, 'planningGenerator')
                .subscribe(s => this.serverErrorMessage = s as string);
        }
        );
    }

    public ngOnDestroy(): void {
        // When exiting the page, remove the active planning :
        this.updateActivePlanning(null);
    }

    protected getBackLinkPath(): string {
        return PlanningGeneratorRouteResolver.baseRoutePath();
    }

    protected setPageTitle(): void {
        this.subscribePageTitle(
            this.translocoService.selectTranslate('planningGenerator.title', { unitName: this.unitDetails?.getUnit().getName() })
        );
    }

    private downloadCurrentPlanning(): void {
        window.open('assets/mocks/epop-api/teamPlannings/2456_20200824_20200920.csv', '_blank');
    }

    private fetchFavoriteEventShift(unitId: number): void {
        // Get all events shift for this planning :
        this.unitService.refreshUnitFavoriteEventShift(unitId);
    }

    private fetchFavoriteShift(unitId: number): void {
        // Get all events shift for this planning :
        this.unitService.getUnitFavoriteShift(unitId);
    }

    private fetchPlanning(id: number): void {
        // this.isLoading = false;
        this.currentTeamPlanning.isLoading(true).useRetry(true).setValue(undefined);
        // Update template (mostly for the requirementGridOptions new value)
        this.cdRef.detectChanges();
        this.resourcePlanningService.getPlanning$(id).pipe(
            // Update store entities :
            tap((data: EpopNormalizedData) => this.epopStoreManager.setNormalizedData(data)),
            restDataErrorStrategy({
                restData: this.currentTeamPlanning,
                message: this.serverErrorMessage
            }),
            tap((data: EpopNormalizedData) => {
                setTimeout(() => {
                    const currentPlanning = this.getPlanning(data);
                    // Set the current planning as active :
                    this.updateActivePlanning(currentPlanning);
                }, 0);
            }),
            tap((data: EpopNormalizedData) => this.currentTeamPlanning.setValue(data)),
            finalize(() => this.currentTeamPlanning.isLoading(false))
        ).subscribe();
    }

    private fetchPlanningConstraintCategories(): void {
        this.constraintService.refreshAllConstraintCategories();
    }

    private fetchPlanningConstraints(id: number): void {
        // Fetch constraint library info (description, label, ...) :
        this.constraintService.refreshAllLibraryConstraints();
        // Fetch tag definitions :
        this.constraintService.refreshAllConstraintTags();
        // Fetch list of favourite constraints :
        this.resourcePlanningService.getFavoritePlanningConstraints$(id).pipe(
            // Update store entities :
            tap((data: Array<FavoritePlanningConstraint>) => this.favoritepPlanningConstraintsStore.add(data))
        ).subscribe();
    }

    private fetchPlanningCounters(id: number): void {
        this.planningCountersStore.remove();
        this.resourcePlanningService.getPlanningCounters$(id).pipe(
            // Update store entities :
            tap((data: Array<TimeCounter>) => this.planningCountersStore.add(data))
        ).subscribe();
    }

    private fetchPlanningRequirements(id: number): void {
        this.planningRequirementsStore.remove();
        this.resourcePlanningService.getPlanningRequirement$(id).pipe(
            // Update store entities :
            tap((data: Array<PlanningRequirement>) => this.planningRequirementsStore.add(data))
        ).subscribe();
    }

    private fetchUnit(unitId: number): void {
        // Get all events shift for this planning :
        this.unitService.refreshUnitDetails(unitId);
    }

    private getPlanning(data: EpopNormalizedData): TeamPlanning {
        return data.entities.teamPlannings[data.result];
    }

    private openConstraintDialog(): void {
        // Open the dialog :
        this.dialog.open(ConstrainstListDialogComponent);
    }

    private openPlanningGeneratorDialog(): void {
        // Open the dialog :
        this.dialog.open(PlanningGeneratorDialogComponent, {
            data: {
                unitId: this.unitId
            } as PlanningGeneratorDialogData
        });
    }

    private savePlanning(): void {
        console.log('TODO : save planing');
    }

    /**
     * Update the active planning with Akita store. Will remove the active planning if null is submitted.
     */
    private updateActivePlanning(planning: TeamPlanning | null): void {
        const currentActivePlanning = this.teamPlanningsQuery.getActiveId() as number;
        if (!planning) {
            this.teamPlanningsStore.setActive(null);
        } else if (currentActivePlanning !== planning.getId()) {
            this.teamPlanningsStore.setActive(planning.getId());
        }
    }
}
