import { ChangeDetectionStrategy, Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { TranslocoService } from '@ngneat/transloco';
import { Observable, Subscription } from 'rxjs';

import { ConfirmDialogComponent } from 'src/app/common/components/app-dialog/components/confirm-dialog/confirm-dialog.component';
import { ActionMenuCellParams } from 'src/app/common/core/grid/components/action-menu-cell/action-menu-cell.component';
import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { ConstraintService } from 'src/app/common/services/epop-api/constraint.service';
import { FavoritePlanningConstraintsQuery } from 'src/app/common/store/favorite-planning-constraints/favorite-planning-constraint.query';
import { PlanningConstraintsQuery } from 'src/app/common/store/planning-constraints/planning-constraint.query';

import { ConstraintListComponent } from '../constraint-list/constraint-list.component';
import { PlanningService } from './../../../../../../common/services/epop-api/planning.service';

@Component({
    selector: 'epop-constraint-favorite-list',
    templateUrl: './constraint-favorite-list.component.html',
    styleUrls: ['./constraint-favorite-list.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintFavoriteListComponent implements OnInit, OnDestroy {

    @Output()
    public readonly editConstraint = new EventEmitter<FavoritePlanningConstraint>();

    @Output()
    public readonly importConstraints = new EventEmitter<void>();

    @Output()
    public readonly closeList = new EventEmitter<void>();

    public updatedConstraintStateList: Record<number, boolean> = [];
    public constraintsList$: Observable<FavoritePlanningConstraint[]>;

    public visibleColumns: ConstraintListComponent['visibleColumns'] = ['targetDefinition', 'period', 'weight', 'actions'];

    public menuActionParams: ActionMenuCellParams = {
        showEnableToggler: (data: FavoritePlanningConstraint) => !this.planningConstraintsQuery.getLibraryConstraint(data).isMandatory,
        defaultToggleEnable: (data: FavoritePlanningConstraint) => data.isEnable,
        menuActions: [
            {
                getLabel: (): string => this.translocoService.translate<string>('common.crud.modify'),
                onClick: (data: FavoritePlanningConstraint): void => this.editConstraint.emit(data)
            },
            {
                getLabel: (): string => this.translocoService.translate<string>('common.crud.delete'),
                onClick: (data: FavoritePlanningConstraint): void => this.deleteFavoriteConstraint(data)
            }
        ],
        onEnable: (constraint: FavoritePlanningConstraint, isEnable: boolean) => this.onEnableRowConstraint(constraint, isEnable)
    };

    private subscriptions: Array<Subscription> = [];

    public constructor(
        public favoritePlanningConstraintsQuery: FavoritePlanningConstraintsQuery,
        public planningConstraintsQuery: PlanningConstraintsQuery,
        public constraintService: ConstraintService,
        public planningService: PlanningService,
        private dialog: MatDialog,
        private translocoService: TranslocoService
    ) { }


    public ngOnInit(): void {
        // Get list of constraints :
        this.constraintsList$ = this.favoritePlanningConstraintsQuery.selectAll();
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public isRowDisabled(constraint: FavoritePlanningConstraint): boolean {
        return constraint && !constraint.isEnable;
    }

    public goToConstraintImportPage(): void {
        this.importConstraints.emit();
    }

    public deleteFavoriteConstraint(constaint: FavoritePlanningConstraint): void {
        ConfirmDialogComponent.openDialog(this.dialog, {
            dialogTitle: this.translocoService.translate('planningGenerator.planning.planningConstraint.delete.confirmTitle'),
            dialogMessage: this.translocoService.translate('planningGenerator.planning.planningConstraint.delete.confirmMessage', { constraintIndex: constaint.constraintLibraryId }),
            onConfirm: () => this.planningService.deleteFavoritePlanningConstraints(constaint.id)
        });
    }

    private onEnableRowConstraint(constraint: FavoritePlanningConstraint, isEnable: boolean): void {
        this.constraintService.updateFavoriteConstraintStateValue(constraint.id, isEnable);
    }

}
