import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PlanningGeneratorPage } from './planning-generator.page';
import { PlanningGeneratorRouteResolver } from './planning-generator-routing.resolver';
import { UnitsPage } from './units/units.page';

const routes: Routes = [
    {
        path: '',
        component: UnitsPage
    },
    {
        path: PlanningGeneratorRouteResolver.route('unit').routerPath,
        component: PlanningGeneratorPage
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PlanningGeneratorRoutingModule { }
