import { ChangeDetectionStrategy, Component } from '@angular/core';
import { add } from 'date-fns';

import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';

export interface PlanningHeaderParams {
    planningDay: Date;
}

@Component({
    selector: 'epop-planning-header',
    templateUrl: './planning-column-header.component.html',
    styleUrls: ['./planning-column-header.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlanningColumnHeaderComponent {

    protected params: PlanningHeaderParams;

    public constructor(private dateHelperService: DateHelperService) {}

    agInit(params: PlanningHeaderParams): void {
        this.params = params;
    }

    getFormattedDateDay(): string {
        return this.dateHelperService.toShortAbbrevatedDay(this.params.planningDay);
    }

    getFormattedDateNumber(): string {
        return DateHelperService.toShortAbbrevatedNumber(this.params.planningDay);
    }

    showCurrentMonth(): boolean {
        return DateHelperService.isLastDayOfMonth(this.params.planningDay);
    }

    getNextMonthString(): string {
        return this.dateHelperService.toMonthAbbrevatedNumber(add(this.params.planningDay, { days: 1 }));
    }

}
