import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { Shift } from 'src/app/common/models/planning/shift.model';

export type ShiftActionsCellParams = {
    value: Shift['id'];
    data: AbstractShift;
    onDelete(shiftId: number): void;
} & Omit<ICellRendererParams, 'value'>;

@Component({
    selector: 'epop-shift-actions-cell',
    templateUrl: './shift-actions-cell.component.html',
    styleUrls: ['./shift-actions-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ShiftActionsCellComponent implements ICellRendererAngularComp {
    public params: ShiftActionsCellParams;

    agInit(params: ShiftActionsCellParams): void {
        this.params = params;
    }

    refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    public onDelete(): void {
        this.params.onDelete(this.params.value);
    }

    public canDelete(): boolean {
        return !(this.params.data as Shift).isNotRemovable();
    }
}
