import { ChangeDetectionStrategy, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';

import { AppPageContainerComponent } from 'src/app/common/components/app-page-container/app-page-container.component';
import { JobTitle } from 'src/app/common/models/planning/job-title.model';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';

import { AppLocalesService } from './../../../../../../common/core/transloco/app-locales.service';
import { LangCode } from './../../../../../../common/models/application-context.model';
import { PlanningRequirement } from './../../../../../../common/models/planning/planning-requirement.model';
import { PlanningService } from './../../../../../../common/services/epop-api/planning.service';
import { JobTitlesQuery } from './../../../../../../common/store/job-titles/job-titles.query';
import { UnitDetailsQuery } from './../../../../../../common/store/unitDetails/unit-details.query';
import { RequirementPlanningChangeEvent } from './components/requirement-matrix-edition/requirement-matrix-edition.component';

export interface RequirementEditionDialogData {
    planningRequirement: PlanningRequirement;
    unitId: number;
}

@Component({
    selector: 'epop-requirement-edition-dialog',
    templateUrl: './requirement-edition-dialog.component.html',
    styleUrls: ['./requirement-edition-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RequirementEditionDialogComponent implements OnInit, OnDestroy {
    @ViewChild(AppPageContainerComponent)
    public pageContainerComponent: AppPageContainerComponent;

    public noneValue = 'none';
    public editedRequirementPlanningValues: Record<string, number> = {};
    public jobTitles: Array<JobTitle>;
    public langCode: LangCode;
    public requiredJobTitleIds: Array<number>;
    public requiredShiftId: number | 'none';
    public shiftList: Array<Shift>;

    private subscriptions: Array<Subscription> = [];

    public constructor(
        public dialogRef: MatDialogRef<RequirementEditionDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: RequirementEditionDialogData,
        private teamPlanningsQuery: TeamPlanningsQuery,
        private planningService: PlanningService,
        private appLocalesService: AppLocalesService,
        private jobTitlesQuery: JobTitlesQuery,
        private unitDetailsQuery: UnitDetailsQuery
    ) { }

    public static openDialog(dialog: MatDialog, data: RequirementEditionDialogData): void {
        dialog.open(RequirementEditionDialogComponent, {
        // To prevent close of dialog on "ESC" touch/outside clicks during input edition:
            disableClose: true,
            data
        });
    }

    public cancelMatrixEdition(): void {
    // TODO: reset matrix
        this.pageContainerComponent.goToPreviousPage();
    }

    public getCurrentPlanning(): TeamPlanning {
        return this.teamPlanningsQuery.getActive() as TeamPlanning;
    }

    public goToDayAffectations(): void {
        this.pageContainerComponent.goToNextPage();
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public ngOnInit(): void {
    // Get list of constraints :
        this.subscriptions.push(
            this.unitDetailsQuery
                .getFavoriteShiftList$(this.data.unitId)
                .subscribe(shiftList => this.shiftList = shiftList),
            this.jobTitlesQuery
                .selectAll()
                .subscribe(jobs => this.jobTitles = jobs)
        );
        // Pre-fill dropdowns :
        this.requiredShiftId = this.data.planningRequirement.getRequiredShiftIds()[0];
        this.requiredJobTitleIds = this.data.planningRequirement.getRequiredJobTitleIds();
        // Get current langage :
        this.langCode = this.appLocalesService.getCurrentAppLanguageCode();
    }

    public onRequirementPlanningChange(event: RequirementPlanningChangeEvent): void {
        const dateKey = PlanningRequirement.getPlanningKey(event.date);
        this.editedRequirementPlanningValues[dateKey] = event.newValue;
    }

    public onSaveExit(): void {
        this.planningService.updatePlanningRequirementValues(
            this.data.planningRequirement,
            {}, // dont need to save matrix values
            this.requiredShiftId !== 'none' ? [this.requiredShiftId] : [],
            this.requiredJobTitleIds
        );
        this.dialogRef.close();
    }

    public onSaveExitMatrixEdition(): void {
        this.planningService.updatePlanningRequirementValues(
            this.data.planningRequirement,
            this.editedRequirementPlanningValues,
            this.requiredShiftId !== 'none' ? [this.requiredShiftId] : [],
            this.requiredJobTitleIds
        );
        this.pageContainerComponent.goToPreviousPage();
    }
}
