import { identifier, serializable } from 'serializr';

import { ObjectHelper } from '../../helpers/ObjectHelper';
import { StringHelper } from '../../helpers/StringHelper';
import { LangCode } from '../application-context.model';


export class JobTitle {

    @serializable(identifier())
    public id: number;

    @serializable
    public nameFr: string;

    @serializable
    public abbreviationFr: string;

    public constructor(init: Partial<JobTitle>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getName(langCode: LangCode): string {
        return this[`name${StringHelper.capitalizeFirstLetter(langCode)}`] as string;
    }

    public getAbbreviation(langCode: LangCode): string {
        return this[`abbreviation${StringHelper.capitalizeFirstLetter(langCode)}`] as string;
    }

    public getFullName(langCode: LangCode): string {
        return `[${this.getAbbreviation(langCode)}] ${this.getName(langCode)}`;
    }

}
