import { ChangeDetectionStrategy, Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { TextInputChangeEvent } from 'src/app/common/components/multi-input-text/components/multi-input-text/multi-input-text.component';
import { Person } from 'src/app/common/models/person.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { ConstraintService } from 'src/app/common/services/epop-api/constraint.service';
import { PlanningConstraintCategoriesQuery } from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';
import { PlanningConstraintsQuery } from 'src/app/common/store/planning-constraints/planning-constraint.query';

import { CollaboratorFilterState } from '../../../../../../common/components/unit/unit-collaborator-list/components/collaborator-filters/collaborator-filters.component';
import { TargetPopulationFilter } from '../../../../../../common/models/planning/favorite-planning-constraint.model';
import { FavoritePlanningConstraint } from './../../../../../../common/models/planning/favorite-planning-constraint.model';


@Component({
    selector: 'epop-constraint-form',
    templateUrl: './constraint-form.component.html',
    styleUrls: ['./constraint-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintFormComponent implements OnInit, OnChanges {

    @Input()
    public constraint: FavoritePlanningConstraint;

    @Input()
    public hideCollaboratorFiltredList: boolean;

    public applyToAllTeam: boolean;
    public weightList: Array<FavoritePlanningConstraint['weight']> = FavoritePlanningConstraint.CONSTRAINT_WEIGHTS;
    public priorityList: Array<FavoritePlanningConstraint['priority']> = FavoritePlanningConstraint.CONSTRAINT_PRIORITY;

    public updatedConstraintParamList: Record<number, FavoritePlanningConstraint['params']> = [];
    public currentPersonFilter: CollaboratorFilterState;
    public collaboratorList: Array<Person> = [];
    public constraintFormControl = new FormGroup({
        weight: new FormControl('', [
            Validators.required
        ]),
        priority: new FormControl('', [
            Validators.required
        ]),
        periodType: new FormControl('full')
    });

    public constraintPeriodControl = new FormGroup({
        startPeriod: new FormControl('', [
            Validators.required
        ]),
        endPeriod: new FormControl('', [
            Validators.required
        ])
    });

    public constructor(private planningConstraintsService: ConstraintService,
        private planningConstraintsQuery: PlanningConstraintsQuery,
        private planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery) {}

    public ngOnInit(): void {
        // Highlight field to complete :
        this.constraintFormControl.markAllAsTouched();
    }

    public getConstraintDef(): PlanningConstraint {
        return this.planningConstraintsQuery.getLibraryConstraint(this.constraint);
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.constraint?.previousValue !== this.constraint) {
            this.updateInitalValues();
        }
    }

    public updateInitalValues(): void {
        this.constraintFormControl.patchValue({
            weight: this.constraint.getWeight(),
            priority: this.constraint.getPriority()
        });
        this.constraintFormControl.patchValue({
            weight: this.constraint.getWeight()
        });
        if (this.constraint.getStartPeriod()) {
            this.patchSelectedPeriodType('custom');
            this.constraintPeriodControl.patchValue({
                startPeriod: this.constraint.getStartPeriod(),
                endPeriod: this.constraint.getEndPeriod()
            });
        } else {
            this.patchSelectedPeriodType('full');
        }
        this.applyToAllTeam = !this.constraint.targetDefinition;
        this.currentPersonFilter = {
            selectedFunctions: this.constraint.targetDefinition?.functions,
            selectedGender: this.constraint.targetDefinition?.genders,
            selectedAges: this.constraint.targetDefinition?.ages,
            selectedWorkRate: this.constraint.targetDefinition?.workRates
        };
    }

    public showPriorityWarning(): boolean {
        const constraintLib = this.getConstraintDef();
        const category = this.planningConstraintCategoriesQuery.getEntity(constraintLib.getCategoryId());
        return this.constraintFormControl.get('priority').value !== category.defaultPriority;
    }

    public getSelectedPeriodType(): FavoritePlanningConstraint['periodType'] {
        return this.constraintFormControl.get('periodType').value as FavoritePlanningConstraint['periodType'];
    }

    public patchSelectedPeriodType(value: FavoritePlanningConstraint['periodType']): void {
        this.constraintFormControl.patchValue({
            periodType: value
        });
    }

    public onTextInputChange(constraint: FavoritePlanningConstraint, newValue: TextInputChangeEvent): void {
        const newParamValue = this.updatedConstraintParamList[constraint.getId()] ?? {};
        newParamValue[newValue.inputKey] = newValue.value;
        this.updatedConstraintParamList[constraint.getId()] = newParamValue;
    }

    public onUnitFilterChanged(event: CollaboratorFilterState): void {
        this.currentPersonFilter = event;
    }

    public onCollaboratorListUpdated(collaboratorList: Array<Person>): void {
        this.collaboratorList = collaboratorList;
    }

    public saveConstraint(): void {
        // Update constraint part :
        let targetDefinition: TargetPopulationFilter;
        if (!this.applyToAllTeam && this.currentPersonFilter) {
            targetDefinition = {
                functions: this.currentPersonFilter.selectedFunctions,
                genders: this.currentPersonFilter.selectedGender,
                ages: this.currentPersonFilter.selectedAges,
                workRates: this.currentPersonFilter.selectedWorkRate
            };
        }
        const periodValue = this.constraintPeriodControl.value as {startPeriod: Date; endPeriod: Date};
        this.planningConstraintsService.upsertFavoriteConstraint(this.constraint, this.constraintFormControl.get('weight').value, this.constraintFormControl.get('priority').value, this.getSelectedPeriodType(), periodValue.startPeriod, periodValue.endPeriod, targetDefinition);

        // Update Constraint params
        Object.keys(this.updatedConstraintParamList).forEach(paramsId => {
            const newValues = this.updatedConstraintParamList[paramsId] as FavoritePlanningConstraint['params'];
            this.planningConstraintsService.updateFavoriteConstraintParamValue(Number(paramsId), newValues);
        });
    }

    public formHasErrors(): boolean {
        return this.constraintFormControl.invalid || (this.getSelectedPeriodType() === 'custom' && this.constraintPeriodControl.invalid);
    }

}
