import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';
import { Observable } from 'rxjs';

import { Person } from 'src/app/common/models/person.model';
import { PlanningEditionContext } from 'src/app/common/models/planning/planning-edition-context.model';
import { PlanningEditionContextQuery } from 'src/app/common/store/planning-edition-context/planning-edition-context.query';
import { ResourcePlanningsQuery } from 'src/app/common/store/resource-plannings/resource-plannings.query';

export type ResourceCellParams = ICellRendererParams & {
    value: number; // Resource ID
};

@Component({
    selector: 'epop-resource-cell',
    templateUrl: './resource-cell.component.html',
    styleUrls: ['./resource-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResourceCellComponent implements ICellRendererAngularComp {
    public params: ResourceCellParams;
    public person: Person;
    public zoomMode$: Observable<PlanningEditionContext['zoomMode']>;

    public constructor(
        private resourcePlanningsQuery: ResourcePlanningsQuery,
        private planningEditionContextQuery: PlanningEditionContextQuery) {
    }

    agInit(params: ResourceCellParams): void {
        this.params = params;
        const ressourceId = this.params.value as number;
        this.getPersonFromRessourceId(ressourceId);
        this.zoomMode$ = this.planningEditionContextQuery.selectCurrentZoomMode$();
    }

    getPersonFromRessourceId(ressourceId: number): void {
        this.person = this.resourcePlanningsQuery.getResource(ressourceId);
    }

    refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
        return false;
    }
}
