import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { Observable, of } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { AbstractShift } from 'src/app/common/models/planning/abstract-shift.model';
import { EventShiftsQuery } from 'src/app/common/store/event-shifts/event-shifts.query';
import { ResourceAffectationsQuery } from 'src/app/common/store/resource-affectations/resource-affectations.query';
import { ShiftsQuery } from 'src/app/common/store/shifts/shifts.query';

import { ResourceAffectation } from '../../models/planning/resource-affectation.model';
import { DayShiftType, ResourceDayAffectation } from './../../models/planning/resource-day-affectation.model';
import { ResourceDayAffectationsState, ResourceDayAffectationsStore } from './resource-day-affectations.store';

@Injectable({ providedIn: 'root' })
export class ResourceDayAffectationsQuery extends QueryEntity<ResourceDayAffectationsState> {
    public constructor(
        protected store: ResourceDayAffectationsStore,
        protected resourceAffectationsQuery: ResourceAffectationsQuery,
        protected eventShiftsQuery: EventShiftsQuery,
        protected shiftsQuery: ShiftsQuery) {
        super(store);
    }

    public selectResourceAffectation$(
        resourceDayAffectationId: ResourceDayAffectation['id'], affectationType?: DayShiftType): Observable<ResourceAffectation> {
        return this.selectEntity(resourceDayAffectationId).pipe(
            switchMap(resourceDayAffectation => {
                if (resourceDayAffectation && (!affectationType || resourceDayAffectation.getDayType() === affectationType)) {
                    const resourceAffectationId = resourceDayAffectation.getResourceAffectationId();
                    return this.resourceAffectationsQuery.selectEntity(resourceAffectationId);
                } else {
                    return of(undefined as ResourceAffectation);
                }
            })
        );
    }

    public selectResourceAffectationShift$(resourceDayAffectationId: ResourceDayAffectation['id']): Observable<AbstractShift> {
        return this.selectEntity(resourceDayAffectationId).pipe(
            switchMap(resourceDayAffectation => {
                if (resourceDayAffectation) {
                    const resourceAffectationId = resourceDayAffectation.getResourceAffectationId();
                    let abstractShiftQuery: QueryEntity<any>;
                    if (resourceDayAffectation.getDayType() === 'event') {
                        abstractShiftQuery = this.eventShiftsQuery;
                    } else {
                        abstractShiftQuery = this.shiftsQuery;
                    }
                    if (resourceAffectationId) {
                        return this.resourceAffectationsQuery.selectEntity(resourceAffectationId).pipe(
                            map(resAff => resAff.getShiftId()),
                            switchMap(shiftId => abstractShiftQuery.selectEntity(shiftId) as Observable<AbstractShift>)
                        );
                    }
                }
                return of(undefined as AbstractShift);
            })
        );
    }


}
