import { identifier, object, serializable } from 'serializr';

import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';

import { ObjectHelper } from './../../helpers/ObjectHelper';


export type DayShiftType = 'event' | 'workAffectation';

export class ResourceDayAffectation {

    @serializable(identifier())
    public id: number;

    @serializable(object(ResourceAffectation))
    public workAffectation: ResourceAffectation['id'];

    @serializable(object(ResourceAffectation))
    public dayEvent: ResourceAffectation['id'];

    @serializable(object(ResourceAffectation))
    public onDutyAffectation: ResourceAffectation['id'];

    @serializable
    public dayType: DayShiftType;

    public constructor(init: Partial<ResourceDayAffectation>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): number {
        return this.id;
    }

    /**
     * Depending on affectation type return a dayEvent or a work affectation ID.
     */
    public getResourceAffectationId(): ResourceAffectation['id'] {
        if (this.dayType === 'event') {
            return this.dayEvent;
        } else {
            return this.workAffectation;
        }
    }

    public getOnDutyAffectationId(): ResourceAffectation['id'] {
        return this.onDutyAffectation;
    }


    public getDayType(): DayShiftType {
        return this.dayType;
    }

    /**
     * Update affectation of the day. Can only be a unique type (event or work)
     *
     * @param shiftType
     * @param newResourceAffectation
     */
    public setDayAffectation(shiftType: DayShiftType, newResourceAffectationId?: ResourceAffectation['id']): void {
        this.dayType = shiftType;
        switch (shiftType) {
            case 'event':
                this.dayEvent = newResourceAffectationId;
                this.workAffectation = undefined;
                break;
            case 'workAffectation':
                this.dayEvent = undefined;
                this.workAffectation = newResourceAffectationId;
                break;
            default:
                break;
        }
    }

    public setOnDutyAffectation(newResourceAffectationId?: ResourceAffectation['id']): void {
        this.onDutyAffectation = newResourceAffectationId;
    }

    public removeDayAffectation(): void {
        this.dayType = undefined;
        this.dayEvent = undefined;
        this.workAffectation = undefined;
    }

    public setDayEvent(dayEvent: ResourceDayAffectation['dayEvent']): void {
        this.dayEvent = dayEvent;
    }

    public setDayType(dayType: ResourceDayAffectation['dayType']): void {
        this.dayType = dayType;
    }

}
