import { identifier, serializable } from 'serializr';

import { ObjectHelper } from './../../helpers/ObjectHelper';

export class RequirementValue {

    @serializable(identifier())
    public id: number;

    @serializable
    public minValue: number;

    public constructor(init: Partial<RequirementValue>) {
        if (init) {
            ObjectHelper.copyObjectAttributes(this, init);
        }
    }

    public getId(): RequirementValue['id'] {
        return this.id;
    }

    public getMinValue(): RequirementValue['minValue'] {
        return this.minValue;
    }

}
