import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { Observable } from 'rxjs';

import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { TeamPlanning } from '../../models/planning/team-planning.model';
import { PersonsQuery } from '../persons/persons.query';
import { Person } from './../../models/person.model';
import { ResourcePlanningsState, ResourcePlanningsStore } from './resource-plannings.store';

@Injectable({ providedIn: 'root' })
export class ResourcePlanningsQuery extends QueryEntity<ResourcePlanningsState> {
    public constructor(
        protected store: ResourcePlanningsStore,
        private personQuery: PersonsQuery) {
        super(store);
    }

    public getResource(resourceId: ResourcePlanning['id']): Person {
        if (resourceId !== undefined) {
            return this.personQuery.getEntity(resourceId);
        } else {
            return undefined;
        }
    }

    public selectPlanningResources$(teamPlanning: TeamPlanning): Observable<ResourcePlanning[]> {
        return this.selectAll(
            {
                filterBy: e => teamPlanning.getResourcePlanningsIds().includes(e.getId())
            }
        );
    }

}
