import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { ResourceAffectation } from 'src/app/common/models/planning/resource-affectation.model';

import { DayShiftType, ResourceDayAffectation } from '../../models/planning/resource-day-affectation.model';

export type ResourceDayAffectationsState = EntityState<ResourceDayAffectation>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'resource-day-affectations' })
export class ResourceDayAffectationsStore extends EntityStore<ResourceDayAffectationsState> {
    public constructor() {
        super();
    }

    /**
   * Update the affectation of a specific day.
   */
    setAffectation(
        dayAffectationId: ResourceDayAffectation['id'], newResourceAffectation: ResourceAffectation,
        type: DayShiftType, dutyAffection: ResourceAffectation): void {
        this.update(dayAffectationId, state => {
            const newState = new ResourceDayAffectation(state);
            newState.setDayAffectation(type, newResourceAffectation?.getId());
            newState.setOnDutyAffectation(dutyAffection?.getId());
            return newState;
        });
    }

}
