
export class MockHelper {

    public static generateRandomBoolean(): boolean {
        return this.selectRandomItem([true, false]);
    }

    public static selectRandomItem<T>(items: Array<T>): T {
        const randomIndex = this.generateRandomInteger(items.length);
        return items[randomIndex];
    }

    /**
     * Generate a random integer. By default, minimum is set to 0. Max is exclusive.
     *
     * @param max
     * @param min
     */
    public static generateRandomInteger(max: number, min = 0): number {
        return Math.floor(Math.random() * (max - min)) + min;
    }

    public static generateRandomColor(): string {
        const letters = '0123456789ABCDEF';
        let color = '#';
        // eslint-disable-next-line no-loops/no-loops
        for (let i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }

}
