import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { combineLatest, Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { ResourceDayAffectationsQuery } from 'src/app/common/store/resource-day-affectations/resource-day-affectations.query';

import { ResourceAffectation } from '../../models/planning/resource-affectation.model';
import { Person } from './../../models/person.model';
import { PersonsQuery } from './../persons/persons.query';
import { ResourcePlanningsQuery } from './../resource-plannings/resource-plannings.query';
import { PlanningRequirementsState, PlanningRequirementsStore } from './planning-requirements.store';

@Injectable({ providedIn: 'root' })
export class PlanningRequirementsQuery extends QueryEntity<PlanningRequirementsState> {
    public constructor(
        protected store: PlanningRequirementsStore,
        protected resourcePlanningsQuery: ResourcePlanningsQuery,
        private personsQuery: PersonsQuery,
        protected resourceDayAffectationsQuery: ResourceDayAffectationsQuery) {
        super(store);
    }

    public computeRequirementValue$(
        planningRequirement: PlanningRequirement,
        teamPlanning: TeamPlanning,
        date: Date): Observable<number> {
        // First get the planning of each resource (PlanningResource)
        return this.resourcePlanningsQuery.selectPlanningResources$(teamPlanning).pipe(
            switchMap(resourcePlannings =>
            // For each resource, get all his affectation of the day specified :
                combineLatest(resourcePlannings.map(resPlan => {
                    const dayAffectationId = resPlan.getResourceDayAffectations(date);
                    const personId = resPlan.getResourceId();
                    // Select only work shifts :
                    return this.resourceDayAffectationsQuery.selectResourceAffectation$(dayAffectationId, 'workAffectation')
                        .pipe(
                            map(shifts => ({ personId, shifts }))
                        );
                }))
            ),
            // For each affectation, count the presence of the required entities :
            map(resourceAffections => {
                let requirementCount = 0;
                resourceAffections.forEach(resourceAffectionData => {
                    const hasAffectationRequirements =
                this.hasResourceAffectationRequirements(planningRequirement, resourceAffectionData.shifts, resourceAffectionData.personId);
                    if (hasAffectationRequirements) {
                        requirementCount++;
                    }
                });
                return requirementCount;
            })
        );
    }

    private hasResourceAffectationRequirements(
        planningRequirement: PlanningRequirement, resourceAffection: ResourceAffectation, resourceId: Person['id']): boolean {
        // Check shifts :
        const hasRequiredShift = this.checkShiftRequirement(planningRequirement.getRequiredShiftIds(), resourceAffection?.getShiftId());
        // Check jobtitles :
        const hasRequiredJobTitle = this.checkJobTitleRequirement(planningRequirement.getRequiredJobTitleIds(), resourceId);

        return hasRequiredShift && hasRequiredJobTitle;
    }

    /**
     * Compute if the shiftId meets the requirements from requiredShiftIds.
     *
     * @param requiredShiftIds
     * @param shiftId
     * @returns
     */
    private checkShiftRequirement(requiredShiftIds: Array<number>, shiftId: number): boolean {
        return requiredShiftIds.length === 0 || requiredShiftIds.includes(shiftId);
    }

    /**
     * Compute if the specified person meets the requirements from requiredJobIds.
     *
     * @param requiredShiftIds
     * @param shiftId
     * @returns
     */
    private checkJobTitleRequirement(requiredJobIds: Array<number>, personId: number): boolean {
        // No requirements, so return true :
        if (requiredJobIds.length === 0) {
            return true;
        } else {
        // Check that the person has the correct jobTitle :
            const person = this.personsQuery.getEntity(personId);
            const resourceJobTitleId = person.getJobTitleId();
            return requiredJobIds.length === 0 || requiredJobIds.includes(resourceJobTitleId);
        }
    }
}
