import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';

import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { ResourcePlanningsQuery } from './../resource-plannings/resource-plannings.query';
import { TeamPlanningsState, TeamPlanningsStore } from './team-plannings.store';

@Injectable({ providedIn: 'root' })
export class TeamPlanningsQuery extends QueryEntity<TeamPlanningsState> {

    public constructor(protected store: TeamPlanningsStore, private resourcePlanningQuery: ResourcePlanningsQuery) {
        super(store);
    }

    public selectResourcePlannings$(planningId: TeamPlanning['id']): Observable<ResourcePlanning[]> {
        return this.selectEntity(planningId).pipe(
            switchMap(planning => this.resourcePlanningQuery.selectAll(
                {
                    filterBy: entity => planning?.getResourcePlanningsIds().includes(entity.getId())
                })
            )
        );
    }

    public selectActivePlanningPeriodDates$(): Observable<[Date, Date]> {
        return (this.selectActive<TeamPlanning>() as Observable<TeamPlanning>).pipe(
            map(planning => planning?.getPeriod())
        );
    }

}
