import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { Observable } from 'rxjs';

import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { FavoritePlanningConstraintsQuery } from 'src/app/common/store/favorite-planning-constraints/favorite-planning-constraint.query';

import { FavoritePlanningConstraint } from '../../models/planning/favorite-planning-constraint.model';
import { PlanningConstraintsState, PlanningConstraintsStore } from './planning-constraint.store';


@Injectable({ providedIn: 'root' })
export class PlanningConstraintsQuery extends QueryEntity<PlanningConstraintsState> {

    public constructor(
        protected store: PlanningConstraintsStore,
        protected favoritePlanningConstraintsQuery: FavoritePlanningConstraintsQuery) {
        super(store);
    }

    /**
     * Select all constraints that can be shown in the library.
     */
    public selectImportableConstraints$(): Observable<PlanningConstraint[]> {
        return this.selectAll();
    }

    /**
     * Check that a constraint can be imported in fav list (depending on duplication parameter)
     */
    public isConstraintImportable(constraint: PlanningConstraint): boolean {
        const favConstraints = this.getAllFavoriteConstraint(constraint);
        return (favConstraints.length < 1 || constraint.isDuplicable);
    }

    public getAllFavoriteConstraint(constraint: PlanningConstraint): Array<FavoritePlanningConstraint> {
        return this.favoritePlanningConstraintsQuery.getAll({
            filterBy: entity => entity?.constraintLibraryId === constraint.id
        });
    }

    public getLibraryConstraint(favConstraint: FavoritePlanningConstraint): PlanningConstraint {
        const libConstraintId = favConstraint.constraintLibraryId;
        return this.getEntity(libConstraintId);
    }

}
