import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';

import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';

import { ConstraintFormComponent } from './../constraint-form/constraint-form.component';


@Component({
    selector: 'epop-constraint-edition',
    templateUrl: './constraint-edition.component.html',
    styleUrls: ['./constraint-edition.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintEditionComponent {

    @Input()
    public constraint: FavoritePlanningConstraint;

    @Output()
    public readonly editionFinished = new EventEmitter<void>();

    @ViewChild(ConstraintFormComponent)
    public constraintFormComponent: ConstraintFormComponent;

    public onSaveExit(): void {
        this.constraintFormComponent.saveConstraint();
        this.editionFinished.emit();
    }

    public onCancelEdition(): void {
        this.editionFinished.emit();
    }

    public editionHasError(): boolean {
        return this.constraintFormComponent?.formHasErrors();
    }

}
