import { ICellRendererParams } from 'ag-grid-community';

export class CellUpdateFlasherHelper {
    private firstRenderedDone = false;
    private params: ICellRendererParams;

    public constructor(params: ICellRendererParams) {
        this.params = params;
    }

    /**
     * Call this methods, when the data changed. This will flash the cell if necessary.
     * This method will prevent the flashing effect for the first rendering of cell.
     */
    public onUpdate(): void {
        if (this.firstRenderedDone) {
            this.params.api.flashCells({
                rowNodes: [this.params.node],
                columns: [this.params.column]
            });
        } else {
            this.firstRenderedDone = true;
        }
    }
}
