import { ChangeDetectionStrategy, Component, TemplateRef, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatMenuTrigger } from '@angular/material/menu';
import { TranslocoService } from '@ngneat/transloco';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { Observable } from 'rxjs';

import { ConfirmDialogComponent } from 'src/app/common/components/app-dialog/components/confirm-dialog/confirm-dialog.component';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { Shift } from 'src/app/common/models/planning/shift.model';
import { JobTitlesQuery } from 'src/app/common/store/job-titles/job-titles.query';
import { ShiftsQuery } from 'src/app/common/store/shifts/shifts.query';

import { AppLocalesService } from './../../../../../../common/core/transloco/app-locales.service';
import { JobTitle } from './../../../../../../common/models/planning/job-title.model';
import { PlanningService } from './../../../../../../common/services/epop-api/planning.service';
import {
    RequirementEditionDialogComponent,
    RequirementEditionDialogData
} from './../requirement-edition-dialog/requirement-edition-dialog.component';

export interface RequirementRowHeaderParams {
    data: PlanningRequirement;
    unitId?: number;
}

interface RequirementMenuItem {
    getLabel(): string;
    onClick?(): void;
}

@Component({
    selector: 'epop-requirement-row-header',
    templateUrl: './requirement-row-header.component.html',
    styleUrls: ['./requirement-row-header.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RequirementRowHeaderComponent implements ICellRendererAngularComp {

    @ViewChild('requirementMenuTrigger', { read: MatMenuTrigger })
    public contextMenuTrigger: MatMenuTrigger;

    @ViewChild('confirmDialogContent')
    public deleteConfirmDialogContent: TemplateRef<any>;

    public params: RequirementRowHeaderParams;
    public menuItems: Array<RequirementMenuItem> = [
        {
            getLabel: (): string => 'Modifier',
            onClick: (): void => this.openEditionDialog()
        },
        {
            getLabel: (): string => 'Supprimer',
            onClick: (): void => this.onDeleteRequirement()
        }
    ];

    public constructor(
        private shiftsQuery: ShiftsQuery,
        private jobTitlesQuery: JobTitlesQuery,
        private planningService: PlanningService,
        private dialog: MatDialog,
        private translocoService: TranslocoService,
        private appLocalesService: AppLocalesService) {}

    public agInit(params: RequirementRowHeaderParams): void {
        this.params = params;
    }

    public refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    public getShiftRequirements$(): Observable<Array<Shift>> {
        return this.shiftsQuery.selectMany(this.params.data.getRequiredShiftIds());
    }

    public getJobTitles$(): Observable<Array<JobTitle>> {
        return this.jobTitlesQuery.selectMany(this.params.data.getRequiredJobTitleIds());
    }

    public getJobTitleLabel(jobTitle: JobTitle): string {
        return jobTitle.getAbbreviation(this.appLocalesService.getCurrentAppLanguageCode());
    }

    private openEditionDialog(): void {
    // Open the dialog :
        RequirementEditionDialogComponent.openDialog(this.dialog, {
            planningRequirement: this.params.data,
            unitId: this.params.unitId
        } as RequirementEditionDialogData);
    }

    private onDeleteRequirement(): void {
        ConfirmDialogComponent.openDialog(this.dialog, {
            dialogTitle: this.translocoService.translate('planningGenerator.requirements.delete.confirmTitle'),
            dialogContent: this.deleteConfirmDialogContent,
            onConfirm: () => this.planningService.deletePlanningRequirement(this.params.data)
        });
    }
}
