import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { JobTitle } from './../../models/planning/job-title.model';

export type JobTitlesState = EntityState<JobTitle>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'jobTitles' })
export class JobTitleStore extends EntityStore<JobTitlesState> {

    public constructor() {
        super();
    }

    public upsertJobTitles(jobTitles: Array<JobTitle>): void {
        this.upsertMany(jobTitles, { baseClass: JobTitle });
    }

}
