import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef, GridApi, GridOptions, GridReadyEvent, ITooltipParams, RowNode, ValueFormatterParams, ValueGetterParams } from 'ag-grid-community';
import { BaseColDefParams } from 'ag-grid-community/dist/lib/entities/colDef';
import { Subscription } from 'rxjs';

import { ActionMenuCellComponent, ActionMenuCellParams } from 'src/app/common/core/grid/components/action-menu-cell/action-menu-cell.component';
import { AppLocalesService } from 'src/app/common/core/transloco/app-locales.service';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { StringHelper } from 'src/app/common/helpers/StringHelper';
import { FavoritePlanningConstraint } from 'src/app/common/models/planning/favorite-planning-constraint.model';
import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';
import { PlanningConstraintCategory } from 'src/app/common/models/planning/planning-constraint-category.model';
import { ConstraintService } from 'src/app/common/services/epop-api/constraint.service';
import { PlanningConstraintCategoriesQuery } from 'src/app/common/store/planning-constraint-categories/planning-constraint-categories.query';
import { PlanningConstraintTagsQuery } from 'src/app/common/store/planning-constraint-tags/planning-constraint-tags.query';
import { PlanningConstraintsQuery } from 'src/app/common/store/planning-constraints/planning-constraint.query';

import { ArrayFilterComponent, ArrayFilterModel } from './../../../../../../common/core/grid/components/filters/array-filter/array-filter.component';
import { AgGridHelper } from './../../../../../../common/core/grid/helpers/ag-grid.helper';
import { JobTitlesQuery } from './../../../../../../common/store/job-titles/job-titles.query';
import { ConstraintCellComponent } from './components/constraint-cell/constraint-cell.component';

export interface EnableConstraintEvent {
    constraint: FavoritePlanningConstraint;
    isEnable: boolean;
}

export type ConstraintFilters = 'params' | 'duplicated' | 'activated' | 'tags';

@Component({
    selector: 'epop-constraint-list',
    templateUrl: './constraint-list.component.html',
    styleUrls: ['./constraint-list.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConstraintListComponent implements OnInit, OnChanges, OnDestroy {
    @Output()
    public readonly enableRowConstraint = new EventEmitter<EnableConstraintEvent>();

    @Output()
    public readonly selectedConstraintsChanged = new EventEmitter<Array<PlanningConstraint>>();

    @Input()
    public checkboxTooltipMessageGetter: (data: PlanningConstraint) => string;

    @Input()
    public constraintsList: Array<PlanningConstraint | FavoritePlanningConstraint>;

    @Input()
    public isRowDisabled: (c: PlanningConstraint | FavoritePlanningConstraint) => boolean;

    @Input()
    public menuActionOption: ActionMenuCellParams;

    @Input()
    public visibleColumns: Array<'targetDefinition' | 'period' | 'weight' | 'actions' | 'checkboxSelector'> = [];

    @Input()
    public visibleFilters: Array<ConstraintFilters>;

    public readonly columnSizes = {
        id: 41,
        checkboxCell: 70,
        label: 360,
        filtre: 140,
        period: 148,
        weight: 100,
        actions: 80
    };

    public readonly defaultYesNoValue = [
        {
            value: true,
            label: this.translocoService.translate<string>('common.yes')
        },
        {
            value: false,
            label: this.translocoService.translate<string>('common.no')
        }
    ];

    public constraintCategoriesList: Array<PlanningConstraintCategory> = [];
    public constraintSearchText = '';
    public defaultColumnDefs: Array<ColDef> = [
        {
            rowGroup: true,
            hide: true,
            initialHide: true,
            width: 40,
            valueGetter: (valParam: ValueGetterParams): number => valParam.data instanceof FavoritePlanningConstraint ? this.getCategoryId(valParam.data) : (valParam.data as PlanningConstraint)?.category,
            valueFormatter: (param: ValueFormatterParams): string => this.constraintCategoriesList.find(g => g.id === Number.parseInt(param.value, 10))?.categoryLabel
        },
        {
            field: 'id',
            // Show the constraintLibraryId in case of favorite; the id otherwhise.
            valueGetter: (valParam: ValueGetterParams): number => valParam.data instanceof FavoritePlanningConstraint ? valParam.data.constraintLibraryId : (valParam.data as PlanningConstraint)?.id,
            headerName: 'ID',
            width: this.columnSizes.id,
            cellClass: 'epop-constraint-list-dialog__content__list__group__id-cell'
        },
        {
            getQuickFilterText: (params): string => this.planningConstraintsQuery.getLibraryConstraint(params.data).getSearchableText(),
            headerName: this.translocoService.translate('planningGenerator.planning.planningConstraint.description'),
            cellRenderer: 'constraintCellComponent',
            autoHeight: true,
            flex: 1
        },
        {
            field: 'tags',
            hide: true,
            filter: 'arrayFilterComponent',
            valueGetter: (valParam: ValueGetterParams): Array<number> => valParam.data instanceof FavoritePlanningConstraint ? this.getTags(valParam.data) : (valParam.data as PlanningConstraint)?.tags
        },
        {
            field: 'isEnable',
            hide: true
        },
        {
            field: 'isDuplicable',
            hide: true
        },
        {
            field: 'hasParams',
            hide: true,
            valueGetter: (params: ValueGetterParams): boolean => (params.data as PlanningConstraint).params !== undefined
        }

    ];

    public filtersDefinition = {
        params: {
            label: this.translocoService.translate<string>('planningGenerator.planning.planningConstraint.filters.params'),
            values: [...this.defaultYesNoValue],
            isMuliple: false
        },
        duplicated: {
            label: this.translocoService.translate<string>('planningGenerator.planning.planningConstraint.filters.duplicated'),
            values: [...this.defaultYesNoValue],
            isMuliple: false
        },
        activated: {
            label: this.translocoService.translate<string>('planningGenerator.planning.planningConstraint.filters.activated'),
            values: [...this.defaultYesNoValue],
            isMuliple: false
        },
        tags: {
            label: this.translocoService.translate<string>('planningGenerator.planning.planningConstraint.filters.tags'),
            values: this.constraintTagsQuery.getAll().map(tag => ({
                label: tag.labelFr,
                value: tag.id
            })) as {value: unknown; label: string}[],
            isMuliple: true
        }
    };

    public frameworkComponents = {
        'constraintCellComponent': ConstraintCellComponent,
        'actionMenuCellComponent': ActionMenuCellComponent,
        'arrayFilterComponent': ArrayFilterComponent
    };

    public gridApi: GridApi;
    public gridOptions: GridOptions = {
        onGridReady: params => this.onGridReady(params),
        suppressContextMenu: true,
        tooltipShowDelay: AgGridHelper.DEFAULT_TOOLTIP_DELAY,
        defaultColDef: {
            suppressMenu: true,
            sortable: true
        },
        rowClassRules: {
            'epop-constraint-list--disabled-row': (params: BaseColDefParams) => params.data ? this.isRowDisabled(params.data) : false
        },
        getRowNodeId: (data: {id: string}) => data.id,
        isRowSelectable: (node: RowNode) => !!node.data,
        rowSelection: 'multiple',
        suppressRowClickSelection: true,
        onSelectionChanged: () => this.selectedConstraintsChanged.emit(this.gridApi.getSelectedRows()),
        rowHeight: 60,
        autoGroupColumnDef: {
            width: 20,
            autoHeight: true,
            headerName: '',
            cellClass: 'epop-constraint-list-dialog__content__list__group',
            cellStyle: { width: '100%' }
        }
    };

    public selectedFilters = {
        params: undefined as boolean,
        duplicated: undefined as boolean,
        activated: undefined as boolean,
        tags: [] as Array<number>
    };

    private subscriptions: Array<Subscription> = [];

    public constructor(
        public planningConstraintsQuery: PlanningConstraintsQuery,
        public planningConstraintCategoriesQuery: PlanningConstraintCategoriesQuery,
        public constraintTagsQuery: PlanningConstraintTagsQuery,
        public constraintService: ConstraintService,
        private translocoService: TranslocoService,
        private appLocalesService: AppLocalesService,
        private jobTitlesQuery: JobTitlesQuery
    ) { }

    public clearSelection(): void {
        this.gridApi.deselectAll();
    }

    public generateRowSelectorColumn(): ColDef {
        return {
            width: this.columnSizes.checkboxCell,
            colId: 'checkboxCell',
            sortable: false,
            headerCheckboxSelection: false,
            checkboxSelection: true,
            headerCheckboxSelectionFilteredOnly: true,
            headerName: '',
            tooltipValueGetter: (params: ITooltipParams): string => this.checkboxTooltipMessageGetter ? this.checkboxTooltipMessageGetter(params.data) : ''
        };
    }

    public getColumnDefinition(): Array<ColDef> {
        return [
            ...(this.visibleColumns?.includes('checkboxSelector') ? [this.generateRowSelectorColumn()] : []),
            ...this.defaultColumnDefs,
            ...this.getOptionalColumns()
        ] as Array<ColDef>;
    }

    public getOptionalColumns(): Array<ColDef> {
        const optionalColumns: Array<ColDef> = [];
        if (this.visibleColumns?.includes('targetDefinition')) {
            optionalColumns.push({
                field: 'targetDefinition',
                headerName: this.translocoService.translate('planningGenerator.planning.planningConstraint.target.label'),
                valueFormatter: (params: ValueFormatterParams): string => this.getTargetDefinitionLabel(params.data),
                width: this.columnSizes.filtre,
                tooltipValueGetter: (params: ITooltipParams): string => this.getTargetDefinitionTooltip(params.data)
            });
        }
        if (this.visibleColumns?.includes('period')) {
            optionalColumns.push({
                field: 'startPeriod',
                headerName: this.translocoService.translate('planningGenerator.planning.planningConstraint.period.label'),
                width: this.columnSizes.period,
                valueFormatter: (params: ValueFormatterParams): string => this.getPeriodLabel(params.data)
            });
        }
        if (this.visibleColumns?.includes('weight')) {
            optionalColumns.push({
                field: 'weight',
                headerName: this.translocoService.translate('planningGenerator.planning.planningConstraint.weight.label'),
                width: this.columnSizes.weight,
                valueFormatter: (params: ValueFormatterParams): string => params.value ? this.translocoService.translate(`planningGenerator.planning.planningConstraint.weight.values.${params.value as string}`) : ''
            });
        }
        if (this.visibleColumns?.includes('actions')) {
            optionalColumns.push({
                field: '',
                headerName: '',
                width: this.columnSizes.actions,
                cellRendererParams: this.menuActionOption,
                cellRenderer: 'actionMenuCellComponent'
            });
        }

        return optionalColumns;
    }

    public getCategoryId(constraint: FavoritePlanningConstraint): number {
        return this.planningConstraintsQuery.getLibraryConstraint(constraint).getCategoryId();
    }

    public getTags(constraint: FavoritePlanningConstraint): Array<number> {
        return this.planningConstraintsQuery.getLibraryConstraint(constraint).tags;
    }

    public getTargetDefinitionLabel(constraint: FavoritePlanningConstraint): string {
        if (constraint) {
            const localeKey = constraint.targetDefinition ? 'planningGenerator.planning.planningConstraint.target.values.teamPart' : 'planningGenerator.planning.planningConstraint.target.values.fullTeam';
            return this.translocoService.translate(localeKey);
        } else {
            return '';
        }
    }

    public getTargetDefinitionTooltip(constraint: FavoritePlanningConstraint): string {
        if (constraint?.targetDefinition) {
            const targetLabels: Array<string> = [];
            const langCode = this.appLocalesService.getCurrentAppLanguageCode();
            // Functions :
            if (constraint.targetDefinition.functions) {
                targetLabels.push(...constraint.targetDefinition.functions.map(functionId => this.jobTitlesQuery.getEntity(functionId)?.getAbbreviation(langCode)));
            }
            // Gender :
            if (constraint.targetDefinition.genders) {
                targetLabels.push(...constraint.targetDefinition.genders.map(genderId => this.translocoService.translate<string>(`person.gender.${genderId}`)));
            }
            // Ages :
            if (constraint.targetDefinition.ages) {
                targetLabels.push(...constraint.targetDefinition.ages.map(ageOption => StringHelper.ageIntervalToString(this.translocoService, ageOption.minAge, ageOption.maxAge)));
            }
            // WorkRates :
            if (constraint.targetDefinition.workRates) {
                targetLabels.push(...constraint.targetDefinition.workRates.map(workRate => `${workRate}%`));
            }
            return targetLabels.join(' - ');
        } else {
            return '';
        }
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.constraintsList) {
            this.computeRowData(changes.constraintsList.previousValue ?? []);
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }

    public ngOnInit(): void {
        // Get list of constraint categories :
        this.subscriptions.push(this.planningConstraintCategoriesQuery.selectAll().subscribe(l => this.constraintCategoriesList = l));
    }

    public onFilterChanged(): void {
        const model = {
            isEnable: {
                type: 'agSetColumnFilter',
                values: this.selectedFilters.activated !== undefined ? [this.selectedFilters.activated.toString()] : undefined
            },
            hasParams: {
                type: 'agSetColumnFilter',
                values: this.selectedFilters.params !== undefined ? [this.selectedFilters.params.toString()] : undefined
            },
            isDuplicable: {
                type: 'agSetColumnFilter',
                values: this.selectedFilters.duplicated !== undefined ? [this.selectedFilters.duplicated.toString()] : undefined
            },
            tags: {
                values: this.selectedFilters.tags?.length > 0 ? this.selectedFilters.tags : undefined
            } as ArrayFilterModel<number>
        };
        this.gridApi?.setFilterModel(model);
    }

    public onGridReady(params: GridReadyEvent): void {
        this.gridApi = params.api;
        if (this.constraintsList) {
            this.gridApi.setRowData(this.constraintsList);
        }
    }

    public onTextFilterChanged(): void {
        // AG grid mode :
        this.gridApi?.setQuickFilter(this.constraintSearchText);
    }

    private computeRowData(oldConstraintList: FavoritePlanningConstraint[]): void {
        AgGridHelper.refreshByTransactions(this.gridApi, oldConstraintList, this.constraintsList);
    }

    private getPeriodLabel(constraintElement: FavoritePlanningConstraint): string {
        if (constraintElement) {
            const periodLabels = constraintElement.getPeriod().map(date => date && DateHelperService.toShortDate(date));
            if (periodLabels[0] !== undefined && periodLabels[1] !== undefined) {
                return this.translocoService.translate(
                    'common.date.period',
                    { startDate: periodLabels[0], endDate: periodLabels[1] }
                );
            }
            return this.translocoService.translate('planningGenerator.planning.planningConstraint.period.values.full');
        }
        return '';
    }
}
