import { Injectable } from '@angular/core';
import { QueryEntity } from '@datorama/akita';
import { combineLatest, Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';

import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';
import { ResourceDayAffectationsQuery } from 'src/app/common/store/resource-day-affectations/resource-day-affectations.query';
import { TeamPlanningsQuery } from 'src/app/common/store/team-plannings/team-plannings.query';

import { ResourcePlanning } from '../../models/planning/resource-planning.model';
import { TimeCounter } from '../../models/planning/time-counter.model';
import { FavoritePlanningConstraintsQuery } from '../favorite-planning-constraints/favorite-planning-constraint.query';
import { PersonsQuery } from './../persons/persons.query';
import { ResourcePlanningsQuery } from './../resource-plannings/resource-plannings.query';
import {
    PlanningCountersState,
    PlanningCountersStore
} from './planning-counters.store';

const WORK_HOURS_CONSTRAINT_ID = 11;
interface WorkHoursConstraintParams {
    nbOfDays: number;
    weekHours: number;
}

@Injectable({ providedIn: 'root' })
export class PlanningCountersQuery extends QueryEntity<PlanningCountersState> {
    public constructor(
        protected store: PlanningCountersStore,
        private resourcePlanningsQuery: ResourcePlanningsQuery,
        private personsQuery: PersonsQuery,
        private teamPlanningsQuery: TeamPlanningsQuery,
        private resourceDayAffectationsQuery: ResourceDayAffectationsQuery,
        private favoriteplanningConstraintsQuery: FavoritePlanningConstraintsQuery
    ) {
        super(store);
    }

    public getCounterOfPerson$(personId: number): Observable<TimeCounter> {
        return this.selectAll().pipe(
            map(counters =>
                counters.find(counter => counter.getPersonId() === personId)
            )
        );
    }

    public computeResourceShiftCounterValue$(
        timeCounter: TimeCounter,
        resourcePlanningId: ResourcePlanning['id']
    ): Observable<number> {
        return this.resourcePlanningsQuery
            .selectEntity(resourcePlanningId)
            .pipe(
                switchMap(resourcePlanning =>
                    // First get all affectations days of the resource :
                    // TODO: Add onDutyAffectations
                    combineLatest(
                        resourcePlanning
                            .getAllDayAffectationIds()
                            .map(dayAffectationId =>
                                this.resourceDayAffectationsQuery.selectResourceAffectationShift$(dayAffectationId)
                            )
                    ).pipe(
                        // Then count hours from shift array :
                        switchMap(affectionShifts => {
                            const person = this.personsQuery.getEntity(
                                timeCounter.getPersonId()
                            );
                            const currentPlanning = this.teamPlanningsQuery.getActive() as TeamPlanning;
                            const period = currentPlanning.getPeriod();
                            // Select the constrainst value :
                            return this.favoriteplanningConstraintsQuery
                                .selectEntity(WORK_HOURS_CONSTRAINT_ID)
                                .pipe(
                                    map(constraint => {
                                        const constraintParams = constraint.getParams() as unknown as WorkHoursConstraintParams;
                                        return timeCounter.computeShiftHoursCount(
                                            affectionShifts,
                                            person.getWorkingPercentage(),
                                            constraintParams.nbOfDays,
                                            constraintParams.weekHours,
                                            period[0],
                                            period[1]
                                        );
                                    })
                                );
                        })
                    )
                )
            );
    }
}
