import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { JobTitle } from './../../models/planning/job-title.model';
import { JobTitleStore } from './../../store/job-titles/job-titles.store';
import { EpopService } from './abstract-epop.service';

@Injectable({
    providedIn: 'root'
})
export class JobTitleService extends EpopService {

    public constructor(
        httpClient: HttpClient,
        private jobTitleStore: JobTitleStore
    ) {
        super(httpClient);
    }

    public getResourcePath(): string {
        return 'jobTitles';
    }

    /**
     * Fetch and store list of job titles
     */
    public refreshAllJobTitles(): void {
        this.getEntity$<Array<JobTitle>>(JobTitle).pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((jobTiles: Array<JobTitle>) => this.jobTitleStore.upsertJobTitles(jobTiles));
    }

    /**
     * Fetch and store job title
     */
    public refreshJobTitle(jobTitleId: number): void {
        this.getEntity$<JobTitle>(JobTitle, jobTitleId.toString()).pipe(
            catchError((error: HttpErrorResponse) => throwError(error.message))
        ).subscribe((jobTitle: JobTitle) => this.jobTitleStore.upsertJobTitles([jobTitle]));
    }

}
