import { LangCode } from './../application-context.model';
import { Person } from './../person.model';
import { JobTitle } from './job-title.model';

export type PlanningZoomMode = 'normal' | 'compacted' | 'ultraCompact';
export type PlanningSortingAttribute = 'byFirstName' | 'byLastName' | 'byFunction';
export type PlanningSortingOrder = 'asc' | 'desc';


export interface PlanningEditionContext {
    cellContentType: 'onlyEvents' | 'shifts';
    planningType: 'planned' | 'onPreparation' | 'generated';
    zoomMode: PlanningZoomMode;
    sortingAttribute: PlanningSortingAttribute;
    sortingOrder: PlanningSortingOrder;
    visualizationPeriod: 'normal' | 'compacted' | 'week' | '2weeks' | '3weeks' | '4weeks';
}

export class PlanningEditionContextHelper {
    static getDaysCountFromVisuPeriod(visualizationPeriod: PlanningEditionContext['visualizationPeriod']): number {
        switch (visualizationPeriod) {
            case 'week':
                return 7;
            case '2weeks':
                return 14;
            case '3weeks':
                return 21;
            case '4weeks':
                return 28;
            default:
                return undefined;
        }
    }

    static getSortingAttributeValue(
        sortingAttribute: PlanningSortingAttribute, resourcePerson: Person, jobTitle: JobTitle, langCode: LangCode): string {
        switch (sortingAttribute) {
            case 'byFirstName':
                return resourcePerson.getFirstname();
            case 'byLastName':
                return resourcePerson.getLastname();
            case 'byFunction':
                return jobTitle.getName(langCode);
            default:
                // By Id:
                return resourcePerson.getId().toString();
        }
    }
}
