import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';

import { ShiftType } from 'src/app/common/models/planning/shift.model';

import { EventShift } from '../../models/planning/event-shift.model';
import { LocalTime } from '../../models/planning/local-time.model';
import { Shift } from '../../models/planning/shift.model';
import { ShiftsStore } from '../../store/shifts/shifts.store';
import { ShiftService } from './../../services/epop-api/shift.service';
import { MockHelper } from './../helpers/mock.helper';

@Injectable({
    providedIn: 'root'
})
export class ShiftMock extends ShiftService {

    private generatedPlanningShiftList: Array<Shift>;

    public constructor(httpClient: HttpClient,
        shiftsStore: ShiftsStore) {
        super(httpClient, shiftsStore);
    }

    /**
     * Get list of event shifts
     */
    public getAllEventShifts$(): Observable<Array<EventShift>> {
        return of(this.generateEventShifts());
    }


    public generateEventShifts(): Array<EventShift> {
        return mockedEventList.map((data: RawEvent): EventShift => new EventShift({
            code: data.code,
            rgbTextColor: MockHelper.generateRandomColor(),
            description: data.description,
            id: data.id !== undefined ? data.id : Number.parseInt(data.code, 10),
            notRemovable: data.notRemovable
        }));
    }

    public getPlanningShiftList(): Array<Shift> {
        if (!this.generatedPlanningShiftList) {
            this.generatedPlanningShiftList = [];
            const tasks = ['C1', 'C2', 'C3', 'C4', 'C5'];
            const colors = ['#8a2be2', '#d2691f', '#fb7f50', '#6495ed', '#8fbc8f'];
            const types: Array<ShiftType> = ['morning', 'day', 'evening', 'night', 'onCallDuty'];
            const startTime = new LocalTime({ hours: 9, minutes: 0 });
            const endTime = new LocalTime({ hours: 18, minutes: 30 });
            const nbMinutesBreakDuration = 30;
            const nbHoursShiftDuration = 8.5;
            const rgbTextColor = '#fff';
            tasks.forEach((code, index) => {
                const rgbBackgroundColor: string = colors[index];
                this.generatedPlanningShiftList.push(new Shift(
                    {
                        id: index,
                        code,
                        rgbBackgroundColor,
                        rgbTextColor,
                        startTime,
                        endTime,
                        nbMinutesBreakDuration,
                        nbHoursShiftDuration,
                        shiftType: types[index]
                    }
                ));
            });
        }
        return this.generatedPlanningShiftList;
    }

}
