import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
    CellValueChangedEvent,
    ColDef,
    GridApi,
    GridOptions,
    GridReadyEvent,
    ICellRendererParams,
    RangeSelectionChangedEvent,
    RowNode,
    ValueFormatterParams,
    ValueGetterParams
} from 'ag-grid-community';

import { NumericCellEditorComponent } from 'src/app/common/core/grid/components/numeric-cell-editor/numeric-cell-editor.component';
import { AgGridHelper } from 'src/app/common/core/grid/helpers/ag-grid.helper';
import { DateHelperService } from 'src/app/common/helpers/DateHelper.service';
import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';
import { TeamPlanning } from 'src/app/common/models/planning/team-planning.model';

import { CheckboxCellComponent, CheckboxCellParams } from './../../../../../../../../common/core/grid/components/checkbox-cell/checkbox-cell.component';
import {
    MatrixSelectionMode,
    RequirementMatrixSelectionHeaderComponent,
    RequirementMatrixSelectionHeaderParams
} from './components/requirement-matrix-selection-header.component/requirement-matrix-selection-header.component';

const cellSize = 20;
interface MatrixCellModel {
    date: Date;
    value: number;
}
interface RowModel {
    day1: MatrixCellModel;
    day2: MatrixCellModel;
    day3: MatrixCellModel;
    day4: MatrixCellModel;
    day5: MatrixCellModel;
    day6: MatrixCellModel;
    day7: MatrixCellModel;
    endDate: Date;
    startDate: Date;
    weekNumber: number;
}

const checkboxCellId = 'checkboxCellId';

export interface RequirementPlanningChangeEvent {
    date: Date;
    newValue: number;
}

@Component({
    selector: 'epop-requirement-matrix-edition',
    templateUrl: './requirement-matrix-edition.component.html',
    styleUrls: ['./requirement-matrix-edition.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RequirementMatrixEditionComponent implements OnInit {
    @Output()
    public readonly requirementPlanningChange: EventEmitter<RequirementPlanningChangeEvent> = new EventEmitter<RequirementPlanningChangeEvent>();

    @Input()
    public planningRequirement: PlanningRequirement;

    @Input()
    public teamPlanning: TeamPlanning;

    public columnDefs: Array<ColDef>;
    public frameworkComponents = {
        numericCellEditorComponent: NumericCellEditorComponent,
        checkboxCellComponent: CheckboxCellComponent,
        requirementMatrixSelectionHeaderComponent: RequirementMatrixSelectionHeaderComponent
    };

    public gridData: Array<Partial<RowModel>> = [];
    public gridOptions: GridOptions = {
        onGridReady: (event: GridReadyEvent) => this.onGridReady(event),
        suppressContextMenu: true,
        defaultColDef: {
            suppressMenu: true
        },
        onRangeSelectionChanged: (rangeEvent: RangeSelectionChangedEvent) => this.onGridSelectionChanged(rangeEvent),
        rowMultiSelectWithClick: true,
        enableRangeSelection: true,
        stopEditingWhenCellsLoseFocus: true
    };

    // Array of values to apply to group selection
    public groupValues = {};
    public selectedColumns: string[] = [];
    public selectionCheckboxHeaderComponent: RequirementMatrixSelectionHeaderComponent;
    // Create array of days (day1, day2, ....)
    public weekDays = Array(7).fill(0).map((_x, i) => `day${i + 1}`);

    private gridApi: GridApi;

    public constructor(private dateHelperService: DateHelperService, private cdRef: ChangeDetectorRef) {}

    public applyGroupValues(): void {
        AgGridHelper.getAllNodeFromRanges(this.gridApi).forEach(node => {
            const colId = node.column.getColId();
            const groupValue = this.groupValues[colId] as number;
            const newData = this.updateMatrixRequirementValue(node.rowNode.data, colId, groupValue);
            // Update data
            node.rowNode.setData(newData);
            // Send updated data to controller :
            this.onCellEdited(newData, colId, groupValue);
        });
        // Recompute checkbox states after grid refresh (as they are re-init)
        this.updateCheckboxStates();
    }

    public ngOnInit(): void {
        this.columnDefs = this.getPlanningGridDefinition();
        this.fillGridData();
    }

    private fillGridData(): void {
        this.gridData = [];
        let rowData: Partial<RowModel>;
        this.teamPlanning.getPlanningDays().forEach(day => {
            if (!rowData) {
                // Init the row data :
                rowData = {
                    startDate: day,
                    weekNumber: DateHelperService.getWeekNumber(day)
                };
            }
            const dayNumber = DateHelperService.getDayNumberInWeek(day);
            rowData[`day${dayNumber}`] = {
                date: day,
                value: this.planningRequirement.getRequirement(day)?.getMinValue() ?? 0
            };
            // If sunday, add the row to data :
            if (dayNumber === 7) {
                rowData.endDate = day;
                this.gridData.push(rowData);
                rowData = undefined;
            }
        });

        // Init groupValue to 0 :
        this.weekDays.forEach(day => this.groupValues[day] = 0);
    }

    private generateDaysColumns(): Array<ColDef> {
        const columns: Array<ColDef> = [];
        const weekDaysLabel = this.dateHelperService.getWeekDaysLabel();
        // eslint-disable-next-line no-loops/no-loops
        for (let i = 1; i <= 7; i++) {
            const colField = `day${i}`;
            const colDef: ColDef = {
                colId: colField,
                headerClass: 'header-matrix',
                width: cellSize,
                cellClass: 'cell-matrix',
                enableCellChangeFlash: true,
                editable: true,
                cellEditor: 'numericCellEditorComponent',
                onCellValueChanged: (params: CellValueChangedEvent) => this.onCellEdited(params.data, params.column.getColId(), params.newValue),
                field: colField,
                valueGetter: (params: ValueGetterParams) => ((params.data as RowModel)[colField] as MatrixCellModel).value,
                valueSetter: params => {
                    this.updateMatrixRequirementValue(params.data, colField, params.newValue);
                    return true;
                },
                headerName: weekDaysLabel[i - 1]
            };
            columns.push(colDef);
        }
        return columns;
    }

    private generateRowSelectorColumn(): ColDef {
        return {
            width: cellSize,
            colId: checkboxCellId,
            headerName: '',
            cellRenderer: 'checkboxCellComponent',
            cellClass: CheckboxCellComponent.getDefaultCellClass(),
            headerComponent: 'requirementMatrixSelectionHeaderComponent',
            headerComponentParams: {
                onSelect: mode => this.onSelectionModeChange(mode),
                onReady: component => this.selectionCheckboxHeaderComponent = component
            } as RequirementMatrixSelectionHeaderParams,
            cellRendererParams: {
                onCheckboxChange: (isChecked, cellParams) => this.onCheckboxRowClicked(isChecked, cellParams),
                stopClickEventPropagation: true
            } as CheckboxCellParams
        };
    }

    private generateStaticColumns(): Array<ColDef> {
        return [{
            width: 135,
            enableCellChangeFlash: true,
            headerName: 'Date de dbut',
            field: 'startDate',
            cellClass: 'matrix-unselectable-cell',
            valueFormatter: (params: ValueFormatterParams): string => DateHelperService.toShortDate(params.value)
        },
        {
            width: 125,
            enableCellChangeFlash: true,
            cellClass: 'matrix-unselectable-cell',
            headerName: 'Date de fin',
            field: 'endDate',
            valueFormatter: (params: ValueFormatterParams): string => DateHelperService.toShortDate(params.value)
        },
        {
            width: 100,
            enableCellChangeFlash: true,
            cellClass: 'matrix-unselectable-cell',
            headerName: 'Semaine',
            field: 'weekNumber'
        }] as Array<ColDef>;
    }

    private getFullSelectedRowIndexes(): Array<RowNode> {
        const fullRowIndexes: Array<RowNode> = [];
        const rowNodesReferences: Array<RowNode> = [];
        // Check completed rows and update checkboxes :
        const nodes = AgGridHelper.getAllNodeFromRanges(this.gridApi);
        const allNodeCounts = [];
        nodes.forEach(element => {
            const rowIndex = element.rowNode.rowIndex;
            if (allNodeCounts[rowIndex] === undefined) {
                allNodeCounts[rowIndex] = {};
                rowNodesReferences[rowIndex] = element.rowNode;
            }
            const colId = element.column.getColId();
            if (colId.startsWith('day')) {
                (allNodeCounts[rowIndex] as RowModel)[colId] = 1;
            }
        });
        allNodeCounts.forEach((rowNodeCount, index) => {
            if (Object.keys(rowNodeCount).length === 7) {
                // This is a full row hehe
                fullRowIndexes.push(rowNodesReferences[index]);
            }
        });
        return fullRowIndexes;
    }

    private getPlanningGridDefinition(): Array<ColDef> {
        return [
            this.generateRowSelectorColumn(),
            ...this.generateDaysColumns(),
            ...this.generateStaticColumns()
        ];
    }

    private onCellEdited(data: RowModel, colId: string, newValue: number): void {
        // Get the date :
        const date = (data[colId] as MatrixCellModel).date;
        // Emit the new values :
        this.requirementPlanningChange.emit({ date, newValue });
    }

    private onCheckboxRowClicked(isChecked: boolean, cellParams: ICellRendererParams): void {
        this.selectCompleteMatrixRow(isChecked, cellParams.node);
        // Upate checkbox header state :
        if (this.selectionCheckboxHeaderComponent !== undefined) {
            this.selectionCheckboxHeaderComponent.resetCheckboxState();
        }
    }

    private onGridReady(event: GridReadyEvent): void {
        this.gridApi = event.api;
    }

    private onGridSelectionChanged(_event: RangeSelectionChangedEvent): void {
    // Update checkbox states :
        this.updateCheckboxStates();
        // Update groupValues view :
        this.updateGroupValuesView();
        // Remove focus to prevent visual glitch after selection :
        this.gridApi.clearFocusedCell();
        // Update view :
        this.cdRef.detectChanges();
    }

    private onSelectionModeChange(mode: MatrixSelectionMode): void {
        switch (mode) {
            case 'all':
                this.selectAllMatrixRows();
                break;
            case 'none':
                this.gridApi.clearRangeSelection();
                break;
            case 'even':
                this.selectOddWeekRows(true);
                break;
            case 'odd':
                this.selectOddWeekRows();
                break;
            default:
                break;
        }
    }

    private selectAllMatrixRows(): void {
        this.gridApi.clearRangeSelection();
        this.gridApi.forEachNode(node => {
            this.selectCompleteMatrixRow(true, node);
        });
    }

    private selectCompleteMatrixRow(setSelected: boolean, node: RowNode): void {
        if (setSelected) {
            this.gridApi.addCellRange({
                rowStartIndex: node.rowIndex,
                rowEndIndex: node.rowIndex,
                columnStart: this.weekDays[0],
                columnEnd: this.weekDays[this.weekDays.length - 1]
            });
        } else {
            // Unselect all node of this row
            AgGridHelper.unselectRowFromRanges(this.gridApi, node.rowIndex);
        }
    }

    private selectOddWeekRows(selectEvenNodes = false): void {
        this.gridApi.clearRangeSelection();
        this.gridApi.forEachNode(node => {
            const weekNumber = DateHelperService.getWeekNumber((node.data as RowModel).day1.date);
            const isOdd = weekNumber % 2 === 1;
            if (!selectEvenNodes && isOdd || selectEvenNodes && !isOdd) {
                this.selectCompleteMatrixRow(true, node);
            }
        });
    }

    private updateCheckboxStates(): void {
        const fullRowIndexes = this.getFullSelectedRowIndexes();
        this.gridApi.forEachNode(node => {
            const cellComponent = this.gridApi.getCellRendererInstances({
                rowNodes: [node],
                columns: [checkboxCellId]
            })[0];
            // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access,@typescript-eslint/no-unsafe-call
            const checkboxCellComponent = (cellComponent as any).getFrameworkComponentInstance() as CheckboxCellComponent;
            // If the row is fully selected :
            if (fullRowIndexes.includes(node)) {
                checkboxCellComponent.setCheckboxState(true);
            } else {
                checkboxCellComponent.setCheckboxState(false);
            }
        });
    }

    private updateGroupValuesView(): void {
        const nodes = AgGridHelper.getAllNodeFromRanges(this.gridApi);
        // Only show and compute groupCell edition with more than 2 elements selected :
        if (nodes.length > 1) {
            const columnCheck = {};
            nodes.forEach(element => {
                const colId = element.column.getColId();
                columnCheck[colId] = true;
            });
            this.selectedColumns = Object.keys(columnCheck);
        } else {
            // Disable groupCell edition :
            this.selectedColumns = [];
        }
    }

    private updateMatrixRequirementValue(data: RowModel, columnKey: string, value: number | null): RowModel {
        (data[columnKey] as MatrixCellModel).value = value === null ? 0 : value;
        return data;
    }
}
