import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { PlanningRequirement } from 'src/app/common/models/planning/planning-requirement.model';

export type PlanningRequirementsState = EntityState<PlanningRequirement>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'planning-requirements' })
export class PlanningRequirementsStore extends EntityStore<PlanningRequirementsState> {
    public constructor() {
        super();
    }

    public updateRequirementPlanningValues(
        requirement: PlanningRequirement,
        newPlanningValues: Record<string, number>,
        requiredShiftIds: number[],
        requiredJobTitles: number[]
    ): void {
        this.upsert(
            requirement.getId(),
            (_oldState: PlanningRequirement) => {
                const isNewEntity = _oldState.id === undefined;
                const newState = new PlanningRequirement(isNewEntity ? requirement : _oldState);
                if (newPlanningValues) {
                    // Create new object reference for planning :
                    newState.planning = { ...newState.planning };
                    newState.setPlanningMinValues(newPlanningValues);
                }
                newState.requiredShifts = [...requiredShiftIds];
                newState.requiredJobTitles = [...requiredJobTitles];
                return newState;
            },
            { baseClass: PlanningRequirement }
        );
    }

    public deleteRequirementPlanning(requirement: PlanningRequirement): void {
        this.remove(requirement.getId());
    }
}
