import { Injectable } from '@angular/core';
import { EntityState, EntityStore, StoreConfig } from '@datorama/akita';

import { PlanningConstraint } from 'src/app/common/models/planning/planning-constraint.model';


export type PlanningConstraintsState = EntityState<PlanningConstraint>;

@Injectable({ providedIn: 'root' })
@StoreConfig({ name: 'planning-constraint' })
export class PlanningConstraintsStore extends EntityStore<PlanningConstraintsState> {

    public constructor() {
        super();
    }

    public upsertConstraints(constraints: Array<PlanningConstraint>): void {
        this.upsertMany(constraints, { baseClass: PlanningConstraint });
    }
}
